import React, { useState, useEffect, useCallback } from 'react';
import './NotificationWidget.css';

interface NotificationWidgetProps {
  isVisible: boolean;
  onClose: () => void;
  onOpenChat?: () => void;
  settings?: {
    message_text: string;
    ai_name: string;
    color_theme: string;
    display_duration: number;
    animation_type: string;
    show_close_button: boolean;
    redirect_url?: string;
    color_theme_css: {
      primary: string;
      secondary: string;
      gradient: string;
    };
  };
}

const NotificationWidget: React.FC<NotificationWidgetProps> = ({ 
  isVisible, 
  onClose, 
  onOpenChat,
  settings 
}) => {
  const [isAnimating, setIsAnimating] = useState(false);
  const [isClosing, setIsClosing] = useState(false);

  const handleClose = useCallback(() => {
    setIsClosing(true);
    setTimeout(() => {
      onClose();
      setIsClosing(false);
      setIsAnimating(false);
    }, 300); // Animation duration
  }, [onClose]);

  useEffect(() => {
    if (isVisible && settings) {
      // Start animation
      setIsAnimating(true);
      
      // Auto close disabled for debugging - widget will stay visible
      // const timer = setTimeout(() => {
      //   handleClose();
      // }, settings.display_duration);

      // return () => clearTimeout(timer);
    }
  }, [isVisible, settings, handleClose]);

  const handleClick = () => {
    // Open chat first
    if (onOpenChat) {
      onOpenChat();
    }
    
    // Then close notification
    handleClose();
  };

  if (!isVisible || !settings) {
    return null;
  }

  const getAnimationClass = () => {
    if (isClosing) return 'notification-widget-closing';
    if (isAnimating) return `notification-widget-${settings.animation_type}`;
    return '';
  };

  return (
    <div 
      className={`notification-widget ${getAnimationClass()}`}
      onClick={handleClick}
    >
      {/* Speech Bubble */}
      <div className="notification-speech-bubble">
        <div className="notification-content">
          <div className="notification-header">
            <span className="notification-sender">{settings?.ai_name || 'ConvState AI'}</span>
          </div>
          <div className="notification-message">
            {settings.message_text}
          </div>
        </div>
        
        {/* Close Button */}
        {settings.show_close_button && (
          <button 
            className="notification-close-btn"
            onClick={(e) => {
              e.stopPropagation();
              handleClose();
            }}
            title="Kapat"
          >
            
          </button>
        )}
      </div>

    </div>
  );
};

export default NotificationWidget;
