import React, { useRef, useEffect } from 'react';
import { Message } from '../../../../types';
import './PriceComparisonMessage.css';

interface PriceComparisonMessageProps {
  message: Message;
  onProductClick?: (product: any) => void;
}

const PriceComparisonMessage: React.FC<PriceComparisonMessageProps> = ({ 
  message, 
  onProductClick 
}) => {
  const comparisonData = message.data?.comparison_data;
  const scrollContainerRef = useRef<HTMLDivElement>(null);
  
  // Scroll functionality for similar products
  useEffect(() => {
    const scrollContainer = scrollContainerRef.current;
    if (!scrollContainer) return;

    const similarProducts = comparisonData?.similar_products || [];
    if (similarProducts.length === 0) return;

    // Set CSS variable for product count
    scrollContainer.style.setProperty('--product-count', similarProducts.length.toString());
    
    // Force update the wrapper width calculation
    const wrapper = scrollContainer.querySelector('.products-grid') as HTMLElement;
    if (wrapper) {
      const cardWidth = 250;
      const gap = 15;
      const padding = 20;
      const calculatedWidth = (similarProducts.length * cardWidth) + ((similarProducts.length - 1) * gap) + padding;
      wrapper.style.width = `${calculatedWidth}px`;
      wrapper.style.minWidth = `${calculatedWidth}px`;
      wrapper.style.maxWidth = `${calculatedWidth}px`;
    }

    let isDown = false;
    let startX: number;
    let scrollLeft: number;

    // Mouse events for drag scrolling
    const handleMouseDown = (e: MouseEvent) => {
      isDown = true;
      scrollContainer.style.cursor = 'grabbing';
      startX = e.pageX - scrollContainer.offsetLeft;
      scrollLeft = scrollContainer.scrollLeft;
    };

    const handleMouseLeave = () => {
      isDown = false;
      scrollContainer.style.cursor = 'grab';
    };

    const handleMouseUp = () => {
      isDown = false;
      scrollContainer.style.cursor = 'grab';
    };

    const handleMouseMove = (e: MouseEvent) => {
      if (!isDown) return;
      e.preventDefault();
      const x = e.pageX - scrollContainer.offsetLeft;
      const walk = (x - startX) * 2;
      scrollContainer.scrollLeft = scrollLeft - walk;
    };

    // Touch events for mobile
    const handleTouchStart = (e: TouchEvent) => {
      startX = e.touches[0].pageX - scrollContainer.offsetLeft;
      scrollLeft = scrollContainer.scrollLeft;
    };

    const handleTouchMove = (e: TouchEvent) => {
      if (!startX) return;
      e.preventDefault();
      const x = e.touches[0].pageX - scrollContainer.offsetLeft;
      const walk = (x - startX) * 2;
      scrollContainer.scrollLeft = scrollLeft - walk;
    };

    const handleTouchEnd = () => {
      startX = 0;
    };

    // Event listeners ekle
    scrollContainer.addEventListener('mousedown', handleMouseDown);
    scrollContainer.addEventListener('mouseleave', handleMouseLeave);
    scrollContainer.addEventListener('mouseup', handleMouseUp);
    scrollContainer.addEventListener('mousemove', handleMouseMove);
    scrollContainer.addEventListener('touchstart', handleTouchStart);
    scrollContainer.addEventListener('touchmove', handleTouchMove);
    scrollContainer.addEventListener('touchend', handleTouchEnd);

    // Cleanup function
    return () => {
      scrollContainer.removeEventListener('mousedown', handleMouseDown);
      scrollContainer.removeEventListener('mouseleave', handleMouseLeave);
      scrollContainer.removeEventListener('mouseup', handleMouseUp);
      scrollContainer.removeEventListener('mousemove', handleMouseMove);
      scrollContainer.removeEventListener('touchstart', handleTouchStart);
      scrollContainer.removeEventListener('touchmove', handleTouchMove);
      scrollContainer.removeEventListener('touchend', handleTouchEnd);
    };
  }, [comparisonData?.similar_products?.length]);
  
  // Error durumu - veri yoksa
  if (!comparisonData) {
    return (
      <div className="price-comparison-message">
        <div className="comparison-container error-state">
          <div className="comparison-header">
            <h3> Fiyat Karlatrmas</h3>
            <div className="error-badge">
              <span>Hata</span>
            </div>
          </div>
          
          <div className="error-content">
            <div className="error-icon"></div>
            <div className="error-details">
              <h4>Veri Yklenemedi</h4>
              <p>Fiyat karlatrmas yaplrken bir hata olutu. Ltfen daha sonra tekrar deneyin.</p>
              <div className="error-actions">
                <button className="retry-button" onClick={() => window.location.reload()}>
                   Tekrar Dene
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }

  const { main_product, similar_products, price_analysis, ai_comparison, comparison_summary } = comparisonData;

  // Varsaylan veriler - ana rn yoksa veya eksikse
  if (!main_product || !main_product.name || !main_product.price) {
    return (
      <div className="price-comparison-message">
        <div className="comparison-container default-data">
          <div className="comparison-header">
            <h3> Fiyat Karlatrmas</h3>
            <div className="default-badge">
              <span>Varsaylan Veri</span>
            </div>
          </div>
          
          <div className="default-content">
            <div className="default-icon"></div>
            <div className="default-details">
              <h4>rnek Fiyat Karlatrmas</h4>
              <p>Gerek rn verisi bulunamad iin rnek bir karlatrma gsteriyoruz.</p>
              
              <div className="sample-comparison">
                <div className="sample-product">
                  <div className="product-info">
                    <h5>rnek rn</h5>
                    <div className="price-info">
                      <span className="price">299.99</span>
                      <span className="currency"></span>
                    </div>
                    <div className="category">Elektronik</div>
                  </div>
                </div>
                
                <div className="sample-analysis">
                  <div className="analysis-item">
                    <span className="label">Fiyat Pozisyonu:</span>
                    <span className="value">Ortalama</span>
                  </div>
                  <div className="analysis-item">
                    <span className="label">Deerlendirme:</span>
                    <span className="value good">yi Deer</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }

  // Eer benzer rn yoksa ve sadece ana rn varsa, basit bir widget gster
  if (!similar_products || similar_products.length === 0) {
    return (
      <div className="price-comparison-message">
        <div className="comparison-container no-data">
          <div className="comparison-header">
            <h3> Fiyat Karlatrmas</h3>
            <div className="no-data-badge">
              <span>Veri Bulunamad</span>
            </div>
          </div>

          <div className="main-product-only">
            <div className="product-card main">
              <div className="product-image">
                <img 
                  src="/imgs/default-product.svg" 
                  alt={main_product.name}
                  onError={(e) => {
                    e.currentTarget.src = '/imgs/default-product.svg';
                  }}
                />
              </div>
              <div className="product-details">
                <h4>{main_product.name}</h4>
                <div className="price-info">
                  <span className="price">{main_product.price}</span>
                  <span className="currency"></span>
                </div>
                <div className="category">{main_product.category}</div>
              </div>
            </div>

            <div className="no-comparison-info">
              <div className="info-icon"></div>
              <div className="info-content">
                <h4>Karlatrma Yaplamad</h4>
                <p>Bu rn iin benzer rnler bulunamad. Fiyat karlatrmas yapabilmek iin daha fazla rn verisi gereklidir.</p>
              </div>
            </div>

            {ai_comparison && ai_comparison.analysis && (
              <div className="ai-suggestion">
                <div className="ai-header">
                  <img src="/imgs/ai-conversion-logo.svg" alt="AI" className="ai-logo" />
                  <span>ConvStateAI Analizi</span>
                </div>
                <div className="ai-content">
                  <p>{ai_comparison.analysis}</p>
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
    );
  }

  const getPricePositionIcon = (position: string) => {
    switch (position) {
      case 'lowest': return '';
      case 'highest': return '';
      case 'below_average': return '';
      case 'above_average': return '';
      default: return '';
    }
  };

  const getPriceRatingColor = (rating: string) => {
    switch (rating) {
      case 'excellent': return '#10b981';
      case 'good': return '#3b82f6';
      case 'fair': return '#f59e0b';
      case 'expensive': return '#ef4444';
      default: return '#6b7280';
    }
  };

  const getPriceRatingText = (rating: string) => {
    switch (rating) {
      case 'excellent': return 'Mkemmel';
      case 'good': return 'yi';
      case 'fair': return 'Orta';
      case 'expensive': return 'Pahal';
      default: return 'Bilinmiyor';
    }
  };

  return (
    <div className="price-comparison-message">
      <div className="comparison-container">
        {/* Header */}
        <div className="comparison-header">
          <h3> Fiyat Karlatrmas</h3>
          <div className="comparison-summary">
            <span className="total-products">
              {comparison_summary.total_products_compared} rn karlatrld
            </span>
            <span className="price-position">
              {getPricePositionIcon(price_analysis.price_position)} {comparison_summary.price_position}
            </span>
          </div>
        </div>

        {/* Main Product */}
        <div className="main-product-section">
          <h4> Ana rn</h4>
          <div className="main-product-card">
            <div className="product-info">
              <h5>{main_product.name}</h5>
              <div className="price-info">
                <span className="price">{main_product.price}</span>
                <span className="currency"></span>
              </div>
              <div className="price-rating">
                <span 
                  className="rating-badge"
                  style={{ backgroundColor: getPriceRatingColor(price_analysis.price_rating) }}
                >
                  {getPriceRatingText(price_analysis.price_rating)}
                </span>
              </div>
            </div>
          </div>
        </div>

        {/* Price Analysis */}
        <div className="price-analysis-section">
          <h4> Fiyat Analizi</h4>
          <div className="analysis-grid">
            <div className="analysis-item">
              <span className="label">Ortalama Fiyat:</span>
              <span className="value">{price_analysis.average_price} </span>
            </div>
            <div className="analysis-item">
              <span className="label">En Dk:</span>
              <span className="value">{price_analysis.min_price} </span>
            </div>
            <div className="analysis-item">
              <span className="label">En Yksek:</span>
              <span className="value">{price_analysis.max_price} </span>
            </div>
            <div className="analysis-item">
              <span className="label">Fark:</span>
              <span className={`value ${price_analysis.price_difference >= 0 ? 'positive' : 'negative'}`}>
                {price_analysis.price_difference >= 0 ? '+' : ''}{price_analysis.price_difference} 
              </span>
            </div>
          </div>
        </div>

        {/* Similar Products */}
        {similar_products && similar_products.length > 0 && (
          <div className="similar-products-section">
            <h4> Benzer rnler</h4>
            <div className="products-scroll-container" ref={scrollContainerRef}>
              <div className="products-grid">
                {similar_products.map((product: any, index: number) => (
                  <div key={index} className="product-card" onClick={() => onProductClick?.(product)}>
                    <div className="product-image">
                      <img 
                        src={product.image || '/imgs/default-product.svg'} 
                        alt={product.name}
                        onError={(e) => {
                          e.currentTarget.src = '/imgs/default-product.svg';
                        }}
                      />
                    </div>
                    <div className="product-details">
                      <h6>{product.name}</h6>
                      <div className="product-price">
                        <span className="price">{product.price}</span>
                        <span className="currency"></span>
                      </div>
                      <div className="product-rating">
                         {product.rating}/5
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </div>
        )}

        {/* AI Analysis */}
        {ai_comparison && (
          <div className="ai-analysis-section">
            <h4> AI Analizi</h4>
            <div className="ai-content">
              <div className="ai-analysis">
                <p>{ai_comparison.analysis}</p>
              </div>
              <div className="ai-recommendation">
                <strong> neri:</strong> {ai_comparison.recommendation}
              </div>
            </div>
          </div>
        )}

        {/* Best Value Product */}
        {comparison_summary.best_value_product && (
          <div className="best-value-section">
            <h4> En yi Deer</h4>
            <div className="best-value-card">
              <div className="product-info">
                <h5>{comparison_summary.best_value_product.name}</h5>
                <div className="price-info">
                  <span className="price">{comparison_summary.best_value_product.price}</span>
                  <span className="currency"></span>
                </div>
                <div className="value-badge">
                   En yi Deer
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default PriceComparisonMessage;
