import React from 'react';
import TTSButton from './TTSButton';
import { getAILogoUrls } from '../../utils/imageUtils';
import './WelcomeMessage.css';

interface WelcomeMessageProps {
  welcomeText: string;
  agentName: string;
  logoUrl?: string;
}

const WelcomeMessage: React.FC<WelcomeMessageProps> = ({ 
  welcomeText, 
  agentName, 
  logoUrl 
}) => {
  const defaultLogoUrl = logoUrl || getAILogoUrls().small;
  return (
    <div className="welcome-message-container">
      <div className="welcome-logo">
        <img src={defaultLogoUrl} alt={agentName} />
      </div>
      <div className="welcome-agent-name">
        {agentName}
      </div>
      <div className="welcome-text">
        {welcomeText}
      </div>
      <div className="welcome-tts">
        <TTSButton text={welcomeText} />
      </div>
    </div>
  );
};

export default WelcomeMessage;
