import { EmbedConfig, UsageToken } from '../types';

export class AuthService {
  private static instance: AuthService;
  private token: string | null = null;
  private userId: string | null = null;
  private projectId: string | null = null;
  private config: EmbedConfig | null = null;
  private usageToken: UsageToken | null = null;

  private constructor() {}

  static getInstance(): AuthService {
    if (!AuthService.instance) {
      AuthService.instance = new AuthService();
    }
    return AuthService.instance;
  }

  setConfig(config: EmbedConfig): void {
    this.config = config;
    this.projectId = config.projectId;
    
    if (config.auth) {
      this.token = config.auth.token || null;
      this.userId = config.auth.userId || null;
    }
    
    // CustomizationToken' da token olarak kullan
    if (config.customizationToken) {
      this.token = config.customizationToken;
    }
  }

  setCredentials(token: string, userId: string): void {
    this.token = token;
    this.userId = userId;
  }

  getAuthHeaders(): Record<string, string> {
    const headers: Record<string, string> = {
      'Content-Type': 'application/json',
      'Accept': 'application/json'
    };

    if (this.projectId) {
      headers['X-Project-ID'] = this.projectId;
    }

    if (this.token) {
      headers['Authorization'] = `Bearer ${this.token}`;
    }

    if (this.userId) {
      headers['X-User-ID'] = this.userId;
    }

    return headers;
  }

  isAuthenticated(): boolean {
    return !!(this.token && this.userId);
  }

  getProjectId(): string | null {
    return this.projectId;
  }

  getUserId(): string | null {
    return this.userId;
  }

  getToken(): string | null {
    return this.token;
  }

  getConfig(): EmbedConfig | null {
    return this.config;
  }

  clearAuth(): void {
    this.token = null;
    this.userId = null;
    this.usageToken = null;
  }

  // ===== TOKEN MANAGEMENT METHODS =====

  /**
   * Usage token bilgilerini set et
   */
  setUsageToken(usageToken: UsageToken): void {
    this.usageToken = usageToken;
  }

  /**
   * Usage token bilgilerini getir
   */
  getUsageToken(): UsageToken | null {
    return this.usageToken;
  }

  /**
   * Token kullanlabilir mi kontrol et
   */
  canUseToken(): boolean {
    if (!this.usageToken) {
      return false;
    }
    return this.usageToken.can_use_token && !this.usageToken.is_expired;
  }

  /**
   * Token'larn sresi dolmu mu?
   */
  isTokenExpired(): boolean {
    if (!this.usageToken) {
      return true;
    }
    return this.usageToken.is_expired;
  }

  /**
   * Kalan token saysn getir
   */
  getRemainingTokens(): number {
    return this.usageToken?.tokens_remaining || 0;
  }

  /**
   * Kullanm yzdesini getir
   */
  getUsagePercentage(): number {
    return this.usageToken?.usage_percentage || 0;
  }

  /**
   * Token reset tarihini getir
   */
  getTokenResetDate(): string | null {
    return this.usageToken?.reset_date || null;
  }

  /**
   * Kalan gn saysn getir
   */
  getDaysUntilReset(): number {
    return this.usageToken?.days_until_reset || 0;
  }

  /**
   * Token durumu hakknda bilgi getir
   */
  getTokenStatus(): {
    canUse: boolean;
    isExpired: boolean;
    remaining: number;
    used: number;
    total: number;
    percentage: number;
    resetDate: string | null;
    daysUntilReset: number;
  } {
    return {
      canUse: this.canUseToken(),
      isExpired: this.isTokenExpired(),
      remaining: this.getRemainingTokens(),
      used: this.usageToken?.tokens_used || 0,
      total: this.usageToken?.tokens_total || 0,
      percentage: this.getUsagePercentage(),
      resetDate: this.getTokenResetDate(),
      daysUntilReset: this.getDaysUntilReset()
    };
  }
}
