import React from 'react';
import { Message, Product, FeedbackData } from '../../types';
import { getAILogoUrls } from '../../utils/imageUtils';
import './MessageItem.css';
// import TTSButton from './TTSButton';
import ProductRecommendationMessage from './templates/ProductRecommendationMessage';
import ProductDetailMessage from './templates/ProductDetailMessage';
import PriceComparisonMessage from './templates/PriceComparisonMessage/PriceComparisonMessage';
import FeedbackMessage from './templates/FeedbackMessage';
import GeneralMessage from './templates/GeneralMessage';
import OrderMessage from './templates/OrderMessage';
import CargoTrackingMessage from './templates/CargoTrackingMessage';
import ShimmerMessage from './ShimmerMessage';
import NoDataMessage from './templates/NoDataMessage';
import ErrorMessage from './templates/ErrorMessage';


interface MessageItemProps {
  message: Message;
  onProductClick: (product: Product) => void;
  onFeedback: (feedback: FeedbackData) => void;
  onAddMessage?: (text: string, role: 'user' | 'agent', products?: any[], additionalData?: any) => void;
  onSendMessage?: (text: string) => void;
}

const MessageItem: React.FC<MessageItemProps> = ({ message, onProductClick, onFeedback, onAddMessage, onSendMessage }) => {
  const isUser = message.role === 'user';

  // Intent bazl component mapping sistemi
  const renderIntentBasedComponent = () => {
    if (isUser) return null; // User mesajlar iin component render etme

    const messageType = message.type || message.intent || 'general';
    
    // Debug log removed for production
    
    switch (messageType) {
      case 'product_recommendation':
      case 'product_inquiry':
      case 'category_browse': // Kategori tarama iin eklendi
      case 'order_inquiry': // Now handles both order inquiry and recommendations
      case 'smart_recommendation':
      case 'trend_products':
      case 'contextual_recommendation':
      case 'category_recommendation':
        return (
          <ProductRecommendationMessage 
            message={message}
            onProductClick={onProductClick}
            onFeedback={onFeedback}
            onAddMessage={onAddMessage}
            onSendMessage={onSendMessage}
          />
        );
        
      case 'product_detail':
        return (
          <ProductDetailMessage 
            message={message}
            onProductClick={onProductClick}
            onAddMessage={onAddMessage}
          />
        );
        
      case 'price_comparison_result':
        return (
          <PriceComparisonMessage 
            message={message}
            onProductClick={onProductClick}
          />
        );
        
      case 'product_link':
        return (
          <div className="message agent-message">
            <div className="message-content-wrapper">
              <div 
                className="product-link-message"
                style={{
                  padding: '12px 16px',
                  backgroundColor: 'rgba(59, 130, 246, 0.1)',
                  border: '1px solid rgba(59, 130, 246, 0.3)',
                  borderRadius: '8px',
                  margin: '8px 0'
                }}
              >
                <div 
                  style={{ 
                    fontSize: '14px', 
                    lineHeight: '1.5',
                    color: '#E5E7EB'
                  }}
                  dangerouslySetInnerHTML={{
                    __html: message.content.replace(
                      /\[([^\]]+)\]\(([^)]+)\)/g,
                      '<a href="$2" target="_blank" rel="noopener noreferrer" style="color: #60A5FA; text-decoration: underline; font-weight: 500;">$1</a>'
                    ).replace(/\*\*([^*]+)\*\*/g, '<strong style="color: #F3F4F6;">$1</strong>')
                  }}
                />
              </div>
            </div>
          </div>
        );
        
      case 'product_search': { // rn arama - rn bulunamadnda NoDataMessage gster
        // rn bulunamadnda NoDataMessage gster
        const products = message.products || message.data?.products || [];
        if (products.length === 0) {
          return (
            <NoDataMessage 
              message={message}
              onFeedback={onFeedback}
            />
          );
        }
        // rn varsa normal ProductRecommendationMessage gster
        return (
          <ProductRecommendationMessage 
            message={message}
            onProductClick={onProductClick}
            onFeedback={onFeedback}
            onAddMessage={onAddMessage}
            onSendMessage={onSendMessage}
          />
        );
      }
        
      case 'feedback':
        return (
          <FeedbackMessage 
            message={message}
            onFeedback={onFeedback}
          />
        );
        
              case 'order_tracking':
          return (
            <OrderMessage 
              message={message}
              onFeedback={onFeedback}
            />
          );
        
      case 'cargo_tracking':
        return (
          <CargoTrackingMessage 
            message={message}
            onFeedback={onFeedback}
          />
        );
        
      case 'campaign_inquiry':
        return (
          <GeneralMessage 
            message={message}
            onFeedback={onFeedback}
          />
        );
        
      case 'shimmer':
        return (
          <ShimmerMessage 
            type={message.shimmerType}
          />
        );
        
      case 'greeting':
      case 'help_request':
      case 'price_inquiry':
      case 'general':
        // Funnel intents - sadece analytics iin, UI'da general message olarak gster
        break;
      case 'capabilities_inquiry':
      case 'contact_request':
      case 'project_info':
      case 'conversion_guidance':
      case 'pricing_guidance':
      case 'demo_request':
      case 'product_recommendations':
        break;
      case 'error':
      case 'token_error':
        // Error mesajlar iin zel component
        if (message.data && message.data.error) {
          return <ErrorMessage message={message} />;
        }
        break;
      default:
        return (
          <GeneralMessage 
            message={message}
            onFeedback={onFeedback}
          />
        );
    }
  };

  // User mesajlar iin basit render
  if (isUser) {
    return (
      <div className="message-group user">
        <div className="message user-message">
          {/* Message Content Wrapper */}
          <div className="message-content-wrapper">
            {message.content}
          </div>
        </div>
      </div>
    );
  }

  // Bot mesajlar iin intent bazl component render
  const logoUrls = getAILogoUrls();
  
  return (
    <div className="message-group">
      <div className="agent-avatar">
        <img src={logoUrls.small} alt="AI Conversion Agent" />
      </div>
      {renderIntentBasedComponent()}
    </div>
  );
};

export default MessageItem;
