import React, { useState } from 'react';
import { Message, FeedbackData } from '../../../../types';
import './OrderMessage.css';
import '../../chatbot.css';
import { buildApiUrl } from '../../../../config/api';

interface OrderMessageProps {
  message: Message;
  onFeedback: (feedback: FeedbackData) => void;
}

interface OrderItem {
  product_id: number;
  name: string;
  quantity: number;
  price: number;
}

interface ShippingInfo {
  courier: string;
  tracking_number: string;
  last_update: string;
  location: string;
  estimated_delivery: string;
}

interface OrderTrackingData {
  order_id: string;
  status: string;
  order_date: string;
  items: OrderItem[];
  shipping: ShippingInfo;
  message: string;
}

interface CargoTrackingData {
  intent: string;
  phase: string;
  order_id: string;
  status: string;
  courier: string;
  tracking_number: string;
  last_update: string;
  estimated_delivery: string;
}

const OrderMessage: React.FC<OrderMessageProps> = ({ message, onFeedback }) => {
  const [orderNumber, setOrderNumber] = useState('');
  const [showOrderDetails, setShowOrderDetails] = useState(false);
  const [showCargoTracking, setShowCargoTracking] = useState(false);
  const [orderData, setOrderData] = useState<OrderTrackingData | null>(null);
  const [cargoData, setCargoData] = useState<CargoTrackingData | null>(null);
  
  const handleFeedback = (isHelpful: boolean) => {
    onFeedback({
      messageId: message.id,
      isHelpful,
      comment: ''
    });
  };

  const handleOrderNumberSubmit = async () => {
    if (orderNumber.trim()) {
      try {
        // Widget ayarlarndan order tracking endpoint'ini kontrol et
        const widgetConfig = (window as any).widgetConfig;
        const orderTrackingEndpoint = widgetConfig?.apiEndpoints?.orderTracking;
        const isOrderEnabled = widgetConfig?.isOrderEnabled;
        
        if (!orderTrackingEndpoint || !isOrderEnabled) {
          // Order tracking endpoint yoksa veya devre dysa "yaknda alacak" mesaj gster
          setOrderData({
            order_id: orderNumber,
            status: 'feature_disabled',
            order_date: new Date().toISOString(),
            items: [],
            shipping: {
              courier: 'N/A',
              tracking_number: 'N/A',
              last_update: 'N/A',
              location: 'N/A',
              estimated_delivery: 'N/A'
            },
            message: 'Bu zellik yaknda alacak'
          });
          setShowOrderDetails(true);
          return;
        }
        
        // Kargo takip API'sine istek gnder
        const response = await fetch(buildApiUrl(`/api/cargo/track/${encodeURIComponent(orderNumber)}`), {
          method: 'GET',
          headers: {
            'Content-Type': 'application/json',
          }
        });

        if (response.ok) {
          const result = await response.json();
          
          if (result.success && result.data) {
            // AI ile eletirilmi kargo verisi
            setCargoData(result.data);
            setShowCargoTracking(true);
          } else if (result.data?.feature_disabled) {
            // zellik kapalysa "yaknda alacak" mesaj gster
            setOrderData({
              order_id: orderNumber,
              status: 'feature_disabled',
              order_date: new Date().toISOString(),
              items: [],
              shipping: {
                courier: 'N/A',
                tracking_number: 'N/A',
                last_update: 'N/A',
                location: 'N/A',
                estimated_delivery: 'N/A'
              },
              message: 'Bu zellik yaknda alacak'
            });
            setShowOrderDetails(true);
          } else {
            // Kargo bulunamad
            setOrderData({
              order_id: orderNumber,
              status: 'not_found',
              order_date: new Date().toISOString(),
              items: [],
              shipping: {
                courier: 'N/A',
                tracking_number: orderNumber,
                last_update: 'N/A',
                location: 'N/A',
                estimated_delivery: 'N/A'
              },
              message: 'Kargo numaras ile kargo bulunamad'
            });
            setShowOrderDetails(true);
          }
        } else {
          // API hatas durumunda fallback
          setOrderData({
            order_id: orderNumber,
            status: 'error',
            order_date: new Date().toISOString(),
            items: [],
            shipping: {
              courier: 'N/A',
              tracking_number: orderNumber,
              last_update: 'N/A',
              location: 'N/A',
              estimated_delivery: 'N/A'
            },
            message: 'Kargo takip srasnda hata olutu'
          });
          setShowOrderDetails(true);
        }
      } catch (error) {
        // Network hatas durumunda fallback
        setOrderData({
          order_id: orderNumber,
          status: 'error',
          order_date: new Date().toISOString(),
          items: [],
          shipping: {
            courier: 'N/A',
            tracking_number: orderNumber,
            last_update: 'N/A',
            location: 'N/A',
            estimated_delivery: 'N/A'
          },
          message: 'Balant hatas olutu'
        });
        setShowOrderDetails(true);
      }
    }
  };

  const formatDate = (dateString: string) => {
    const date = new Date(dateString);
    return date.toLocaleDateString('tr-TR');
  };

  const formatDateTime = (dateString: string) => {
    const date = new Date(dateString);
    return date.toLocaleString('tr-TR');
  };

  // If this is an order tracking request, show input field
  if (message.intent === 'order_tracking' && !showOrderDetails && !showCargoTracking) {
    return (
      <div className="message agent-message">
        <div className="message-content-wrapper">
          <p>Kargo takip numaranz veya sipari numaranz girin:</p>
          
          <div className="order-input-container">
            <input
              type="text"
              placeholder="Sipari/Kargo numaras girin..."
              value={orderNumber}
              onChange={(e) => setOrderNumber(e.target.value)}
              className="order-number-input"
            />
            <button 
              onClick={handleOrderNumberSubmit}
              className="track-order-button"
              disabled={!orderNumber.trim()}
            >
              Takip Et
            </button>
          </div>
        </div>
        
        <div className="feedback-container">
          <button 
            className="feedback-button"
            onClick={() => handleFeedback(true)}
          >
             Yararl
          </button>
          <button 
            className="feedback-button negative"
            onClick={() => handleFeedback(false)}
          >
             Yararsz
          </button>
        </div>
      </div>
    );
  }

  // Show simplified cargo tracking if available
  if (showCargoTracking && cargoData) {
    const statusText = cargoData.status === 'in_transit' ? 'Yolda' : 
                      cargoData.status === 'delivered' ? 'Teslim Edildi' : 
                      cargoData.status === 'pending' ? 'Hazrlanyor' : cargoData.status;

    return (
      <div className="message agent-message">
        <div className="message-content-wrapper">
          <div className="cargo-tracking-simple">
            <h3>Kargo Takip Bilgileri</h3>
            
            <div className="cargo-info-grid">
              <div className="cargo-info-item">
                <strong>Sipari No:</strong>
                <span>{cargoData.order_id}</span>
              </div>
              
              <div className="cargo-info-item">
                <strong>Durum:</strong>
                <span className={`status-badge ${cargoData.status}`}>{statusText}</span>
              </div>
              
              <div className="cargo-info-item">
                <strong>Kargo Firmas:</strong>
                <span>{cargoData.courier}</span>
              </div>
              
              <div className="cargo-info-item">
                <strong>Takip Numaras:</strong>
                <span className="tracking-number">{cargoData.tracking_number}</span>
              </div>
              
              <div className="cargo-info-item">
                <strong>Son Gncelleme:</strong>
                <span>{formatDateTime(cargoData.last_update)}</span>
              </div>
              
              <div className="cargo-info-item">
                <strong>Tahmini Teslim:</strong>
                <span>{formatDate(cargoData.estimated_delivery)}</span>
              </div>
            </div>
          </div>
        </div>
        
        <div className="feedback-container">
          <button 
            className="feedback-button"
            onClick={() => handleFeedback(true)}
          >
             Yararl
          </button>
          <button 
            className="feedback-button negative"
            onClick={() => handleFeedback(false)}
          >
             Yararsz
          </button>
        </div>
      </div>
    );
  }

  // Show order details if available
  if (showOrderDetails && orderData) {
    return (
      <div className="message agent-message">
        <div className="message-content-wrapper">
          <div className="order-tracking-details">
            {orderData.status === 'feature_disabled' ? (
              <div className="feature-disabled-message">
                <div style={{ fontSize: '48px', marginBottom: '16px', textAlign: 'center' }}></div>
                <h3 style={{ textAlign: 'center', color: '#92400E', marginBottom: '16px' }}>
                  Bu zellik yaknda alacak
                </h3>
                <p style={{ textAlign: 'center', color: '#92400E', fontSize: '14px', margin: 0 }}>
                  Sipari takip zellii gelitirme aamasnda
                </p>
              </div>
            ) : orderData.status === 'not_found' ? (
              <div className="cargo-not-found-message">
                <div style={{ fontSize: '48px', marginBottom: '16px', textAlign: 'center' }}></div>
                <h3 style={{ textAlign: 'center', color: '#DC2626', marginBottom: '16px' }}>
                  Kargo bulunamad
                </h3>
                <p style={{ textAlign: 'center', color: '#DC2626', fontSize: '14px', margin: 0 }}>
                  {orderData.message}
                </p>
                <div style={{ textAlign: 'center', marginTop: '16px' }}>
                  <button 
                    onClick={() => {
                      setShowOrderDetails(false);
                      setOrderNumber('');
                    }}
                    style={{
                      padding: '8px 16px',
                      backgroundColor: '#3B82F6',
                      color: 'white',
                      border: 'none',
                      borderRadius: '6px',
                      cursor: 'pointer'
                    }}
                  >
                    Tekrar Dene
                  </button>
                </div>
              </div>
            ) : orderData.status === 'error' ? (
              <div className="cargo-error-message">
                <div style={{ fontSize: '48px', marginBottom: '16px', textAlign: 'center' }}></div>
                <h3 style={{ textAlign: 'center', color: '#DC2626', marginBottom: '16px' }}>
                  Hata olutu
                </h3>
                <p style={{ textAlign: 'center', color: '#DC2626', fontSize: '14px', margin: 0 }}>
                  {orderData.message}
                </p>
                <div style={{ textAlign: 'center', marginTop: '16px' }}>
                  <button 
                    onClick={() => {
                      setShowOrderDetails(false);
                      setOrderNumber('');
                    }}
                    style={{
                      padding: '8px 16px',
                      backgroundColor: '#3B82F6',
                      color: 'white',
                      border: 'none',
                      borderRadius: '6px',
                      cursor: 'pointer'
                    }}
                  >
                    Tekrar Dene
                  </button>
                </div>
              </div>
            ) : (
              <>
                <h3>Sipari Takip Bilgileri</h3>
            
            <div className="order-header">
              <div className="order-id">Sipari No: {orderData.order_id}</div>
              <div className="order-status">Durum: {orderData.status === 'shipped' ? 'Kargoya Verildi' : orderData.status}</div>
              <div className="order-date">Sipari Tarihi: {formatDate(orderData.order_date)}</div>
            </div>

            <div className="order-items">
              <h4>Sipari Edilen rnler:</h4>
              {orderData.items.map((item, index) => (
                <div key={index} className="order-item">
                  <span className="item-name">{item.name}</span>
                  <span className="item-quantity">x{item.quantity}</span>
                  <span className="item-price">{item.price}</span>
                </div>
              ))}
            </div>

            <div className="shipping-info">
              <h4>Kargo Bilgileri:</h4>
              <div className="shipping-detail">
                <strong>Kargo Firmas:</strong> {orderData.shipping.courier}
              </div>
              <div className="shipping-detail">
                <strong>Takip Numaras:</strong> {orderData.shipping.tracking_number}
              </div>
              <div className="shipping-detail">
                <strong>Son Gncelleme:</strong> {formatDateTime(orderData.shipping.last_update)}
              </div>
              <div className="shipping-detail">
                <strong>Konum:</strong> {orderData.shipping.location}
              </div>
              <div className="shipping-detail">
                <strong>Tahmini Teslim:</strong> {formatDate(orderData.shipping.estimated_delivery)}
              </div>
            </div>

            <div className="order-message">
              <p>{orderData.message}</p>
            </div>
              </>
            )}
          </div>
        </div>
        
        <div className="feedback-container">
          <button 
            className="feedback-button"
            onClick={() => handleFeedback(true)}
          >
             Yararl
          </button>
          <button 
            className="feedback-button negative"
            onClick={() => handleFeedback(false)}
          >
             Yararsz
          </button>
        </div>
      </div>
    );
  }

  // Default order inquiry display
  return (
    <div className="message agent-message">
      <div className="message-content-wrapper">
        <p>{message.content || message.message || 'Sipari mesaj bulunamad.'}</p>
        
        {/* API'den gelen suggestions kaldrld - sadece default action buttons kullanlyor */}
      </div>
      
      <div className="feedback-container">
        <button 
          className="feedback-button"
          onClick={() => handleFeedback(true)}
        >
           Yararl
        </button>
        <button 
          className="feedback-button negative"
          onClick={() => handleFeedback(false)}
        >
           Yararsz
        </button>
      </div>
    </div>
  );
};

export default OrderMessage;
