import { useState, useCallback, useRef, useEffect } from 'react';
import { Message, Product, FeedbackData } from '../types';
import { useAIService } from './useAIService';
import { uuidService } from '../services/UuidService';

export interface ChatState {
  messages: Message[];
  isTyping: boolean;
  scrollToBottom: boolean;
}

export const useChat = (aiApiKey?: string, projectId?: string) => {
  const [messages, setMessages] = useState<Message[]>([]);
  const [isTyping, setIsTyping] = useState(false);
  const [scrollToBottom, setScrollToBottom] = useState(false);
  const [browserUuid, setBrowserUuid] = useState<string>('');
  const [sessionExpiry, setSessionExpiry] = useState<Date | null>(null);
  const chatContentRef = useRef<HTMLDivElement>(null);

  // AI Service
  const { generateAIResponse: aiServiceGenerateAIResponse, sendFeedback, isLoading: aiLoading } = useAIService({ apiKey: aiApiKey, projectId: projectId });

  /**
   * Browser UUID'yi yenile
   */
  const refreshBrowserUuid = useCallback(() => {
    const newUuid = uuidService.refreshUuid();
    setBrowserUuid(newUuid);
    
    const uuidInfo = uuidService.getUuidInfo();
    if (uuidInfo) {
      setSessionExpiry(uuidInfo.expires);
    }
    
    // //console.log('Browser UUID refreshed:', newUuid);
  }, []);

  // Browser UUID Management
  useEffect(() => {
    // Browser UUID'yi al veya olutur
    const uuid = uuidService.getBrowserUuid();
    setBrowserUuid(uuid);
    
    // UUID bilgilerini al
    const uuidInfo = uuidService.getUuidInfo();
    if (uuidInfo) {
      setSessionExpiry(uuidInfo.expires);
    }
    
    // //console.log('Browser UUID initialized:', uuid);
  }, []);

  // UUID expiration check
  useEffect(() => {
    if (sessionExpiry) {
      const checkExpiry = setInterval(() => {
        if (new Date() >= sessionExpiry) {
          refreshBrowserUuid();
        }
      }, 60000); // Her dakika kontrol et

      return () => clearInterval(checkExpiry);
    }
  }, [sessionExpiry, refreshBrowserUuid]);

  /**
   * Session' yenile (UUID'yi gncelle)
   */
  const refreshSession = useCallback(() => {
    const uuidInfo = uuidService.getUuidInfo();
    if (uuidInfo && uuidInfo.isValid) {
      setSessionExpiry(uuidInfo.expires);
    } else {
      refreshBrowserUuid();
    }
  }, [refreshBrowserUuid]);

  /**
   * Session' temizle
   */
  const clearSession = useCallback(() => {
    uuidService.clearUuid();
    setBrowserUuid('');
    setSessionExpiry(null);
    // //console.log('Browser UUID cleared');
  }, []);

  // Helper function to determine shimmer type based on user message
  const getShimmerType = useCallback((userMessage: string): 'general' | 'product' | 'cargo' | 'order' | 'feedback' => {
    const message = userMessage.toLowerCase();
    
    if (message.includes('rn') || message.includes('product') || message.includes('satn al')) {
      return 'product';
    } else if (message.includes('kargo') || message.includes('cargo') || message.includes('kargom') || message.includes('takip') || message.includes('tracking')) {
      return 'cargo';
    } else if (message.includes('sipari') || message.includes('order') || message.includes('sipariim')) {
      return 'order';
    } else if (message.includes('feedback') || message.includes('geri bildirim')) {
      return 'feedback';
    } else {
      return 'general';
    }
  }, []);

  // Add message function
  const addMessage = useCallback((text: string, role: 'user' | 'agent', products?: Product[], additionalData?: any) => {
    console.log('addMessage called with:', { text, role, products, additionalData });
    
    // Duplicate message prevention - only for agent messages, not user messages
    if (role === 'agent') {
      const now = Date.now();
      const recentMessages = messages.slice(-3); // Check last 3 messages
      
      const isDuplicate = recentMessages.some(msg => {
        const timeDiff = now - msg.timestamp.getTime();
        return timeDiff < 1000 && // Within 1 second
               msg.content === text && 
               msg.role === role;
      });
      
      if (isDuplicate) {
        console.log('Duplicate message prevented');
        return;
      }
    }
    
    const newMessage: Message = {
      id: `${Date.now()}_${Math.random().toString(36).substr(2, 9)}`,
      content: text,
      role,
      timestamp: new Date(),
      products: products || [],
      ...additionalData // type, data, intent gibi ek alanlar ekle (suggestions kaldrld)
    };

    // User mesajlar iin zel koruma - 10 saniye boyunca silinmesin
    if (role === 'user') {
      newMessage.protected = true;
      newMessage.protectionExpiry = new Date(Date.now() + 10000); // 10 saniye sonra
    }

    console.log('Setting messages, current count:', messages.length, 'new message:', newMessage);
    setMessages(prev => {
      const updated = [...prev, newMessage];
      console.log('Updated messages count:', updated.length);
      return updated;
    });
    setScrollToBottom(true);
  }, []);

  // Enhanced AI response with suggestions and shimmer loading
  const generateAIResponse = useCallback(async (userMessage: string) => {
    setIsTyping(true);
    

    
    // Session' refresh et (her AI interaction'da)
    refreshSession();
    
    // Shimmer loading state'i - messages array'e ekleme, sadece UI state olarak kullan
    const shimmerType = getShimmerType(userMessage);
    
    // Shimmer message' ekle
    const shimmerMessage = {
      id: `shimmer_${Date.now()}`,
      role: 'agent' as const,
      content: '',
      type: 'shimmer',
      shimmerType: shimmerType,
      timestamp: new Date()
    };
    
    // Shimmer message' ekle
    setMessages(prev => [...prev, shimmerMessage]);
    
    try {
      // Random delay between 1500ms and 2500ms (daha uzun sre)
      const delay = Math.random() * (2500 - 1500) + 1500;
      await new Promise(resolve => setTimeout(resolve, delay));
      
      // Use the new API-based AI response function
      const aiResponse = await aiServiceGenerateAIResponse(userMessage);
      
      // Debug log
      console.log('AI Response:', aiResponse);
      
      // Response validation - products field'n kontrol et
      if (!aiResponse.products || aiResponse.products.length === 0) {
        if (aiResponse.data && aiResponse.data.products && aiResponse.data.products.length > 0) {
          aiResponse.products = aiResponse.data.products;
        }
      }
      
      // Add AI response with all data from Laravel
      const additionalData = {
        type: aiResponse.type || 'general',
        intent: aiResponse.intent || aiResponse.type || 'general',
        suggestions: aiResponse.suggestions || [],
        data: aiResponse.data || {},
        session_id: browserUuid // Mevcut browser UUID'yi kullan
      };

      // Products' doru ekilde extract et
      const products = aiResponse.products || aiResponse.data?.products || [];

      // Response'u widget'a eklemeden nce daha uzun bir delay ekle
      await new Promise(resolve => setTimeout(resolve, 300));

      // Shimmer message' kaldr (sadece shimmer type' olan mesajlar)
      // Protected user mesajlarn koru
      setMessages(prev => prev.filter(msg => {
        if (msg.type === 'shimmer') return false;
        if (msg.protected && msg.protectionExpiry && msg.protectionExpiry > new Date()) return true;
        return true;
      }));

      // Message content'i kontrol et - bo olmamal
      const messageContent = aiResponse.message || aiResponse.type || 'zgnm, u anda yant veremiyorum.';
      
      // Debug log
      console.log('Adding message:', messageContent, 'Role: agent');
      
      addMessage(messageContent, 'agent', products, additionalData);
      
      // API'den gelen suggestions kaldrld - sadece default action buttons kullanlyor
      
    } catch (error) {
      // AI response error
      // Error in generateAIResponse
      
      // Shimmer message' kaldr (sadece shimmer type' olan mesajlar)
      // Protected user mesajlarn koru
      setMessages(prev => prev.filter(msg => {
        if (msg.type === 'shimmer') return false;
        if (msg.protected && msg.protectionExpiry && msg.protectionExpiry > new Date()) return true;
        return true;
      }));
      
      // Fallback response
      addMessage('zgnm, u anda yant retemiyorum. Ltfen daha sonra tekrar deneyin.', 'agent');
    } finally {
      setIsTyping(false);
    }
  }, [aiServiceGenerateAIResponse, addMessage, browserUuid, refreshSession, getShimmerType]);



  // Send message function
  const sendMessage = useCallback(async (text: string) => {
    if (!text.trim()) return;
    
    // Add user message immediately with 10 second protection
    addMessage(text, 'user');
    
    // Small delay to ensure user message is added to UI
    await new Promise(resolve => setTimeout(resolve, 100));
    
    try {
      // Generate AI response
      await generateAIResponse(text);
    } catch (error) {
      // Error handling is done in generateAIResponse
      // Error generating AI response
    }
  }, [addMessage, generateAIResponse]);

  // Handle product click
  const handleProductClick = useCallback((product: Product) => {
    // Add product selection message with product URL if available
    if (product.url) {
      addMessage(` rn detay sayfasna ynlendiriliyorsunuz:\n\n[${product.url}](${product.url})`, 'agent');
    } else {
      addMessage(`"${product.name}" rnn setiniz. Bu rn hakknda daha fazla bilgi almak ister misiniz?`, 'agent');
    }
  }, [addMessage]);



  // Handle feedback
  const handleFeedback = useCallback((feedback: FeedbackData) => {
    // Feedback'i API'ye gnder (session'a post at)
    sendFeedback(feedback);
    
    // Ama kullancya ekstra mesaj gsterme
    // addMessage(response, 'agent'); // Bu satr kaldrld
  }, [sendFeedback]);

  // Typing indicator - HTTP API ile alyor
  const sendTypingIndicator = useCallback((isTyping: boolean) => {
    // HTTP API ile typing indicator gnderilebilir (gerekirse)
  }, []);

  // Scroll to bottom effect
  useEffect(() => {
    if (scrollToBottom && chatContentRef.current) {
      chatContentRef.current.scrollTop = chatContentRef.current.scrollHeight;
      setScrollToBottom(false);
    }
  }, [scrollToBottom, messages]);

  // Auto-scroll when new messages arrive
  useEffect(() => {
    if (chatContentRef.current) {
      chatContentRef.current.scrollTop = chatContentRef.current.scrollHeight;
    }
  }, [messages]);

  // Send typing indicator when AI is processing
  useEffect(() => {
    sendTypingIndicator(isTyping);
  }, [isTyping, sendTypingIndicator]);

  return {
    messages,
    isTyping,
    aiLoading,
    chatContentRef,
    sendMessage,
    handleProductClick,
    handleFeedback,
    addMessage,
    generateAIResponse,
    // Session Management
    browserUuid,
    sessionExpiry,
    refreshBrowserUuid,
    refreshSession,
    clearSession
  };
};
