import React from 'react';
import { Message, FeedbackData } from '../../../../types';
import './GeneralMessage.css';
import '../../chatbot.css';

interface GeneralMessageProps {
  message: Message;
  onFeedback: (feedback: FeedbackData) => void;
}

const GeneralMessage: React.FC<GeneralMessageProps> = ({ message, onFeedback }) => {

  
  const handleFeedback = (isHelpful: boolean) => {
    onFeedback({
      messageId: message.id,
      isHelpful,
      comment: ''
    });
  };

  return (
    <div className="message agent-message">
      {/* Message Content Wrapper */}
      <div className="message-content-wrapper">
        {/* Mesaj ierii */}
        <p>{message.content || message.message || 'Mesaj ierii bulunamad.'}</p>
        
        {/* API'den gelen suggestions kaldrld - sadece default action buttons kullanlyor */}
      </div>
      
      {/* Feedback Buttons */}
      <div className="feedback-container">
        <button 
          className="feedback-button"
          onClick={() => handleFeedback(true)}
        >
           Yararl
        </button>
        <button 
          className="feedback-button negative"
          onClick={() => handleFeedback(false)}
        >
           Yararsz
        </button>
      </div>
    </div>
  );
};

export default GeneralMessage;
