import { useState, useCallback } from 'react';
import { buildApiUrl, getApiEndpoint } from '../config/api';
import { uuidService } from '../services/UuidService';
import { ApiService } from '../services/ApiService';
import { errorService } from '../services/ErrorService';
import { APIError } from '../types';

interface AIResponse {
  type: string;
  message: string;
  products?: any[];
  intent?: string;
  confidence?: number;
  suggestions?: string[];
  data?: any;
  session_id?: string;
}

interface AIServiceOptions {
  apiKey?: string;
  model?: string;
  temperature?: number;
  maxTokens?: number;
  projectId?: string;
}

export const useAIService = (options: AIServiceOptions = {}) => {
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const apiService = ApiService.getInstance();

  // Browser UUID management sistemi
  const getBrowserUuid = useCallback(() => {
    return uuidService.getBrowserUuid();
  }, []);

  // API baarsz olduunda sadece hata mesaj dndr
  const generateLocalResponse = useCallback((userMessage: string, context?: any): AIResponse => {
    return {
      type: 'error',
      message: 'API balants kurulamad. Ltfen daha sonra tekrar deneyin.',
      products: [],
      intent: 'error',
      confidence: 0.0,
      suggestions: ['Tekrar dene', 'Yardm al']
    };
  }, []);

  // AI Service with API integration - Ana fonksiyon
  const generateAIResponse = useCallback(async (userMessage: string, context?: any): Promise<AIResponse> => {
    try {
      setIsLoading(true);
      setError(null);
      
      const browserUuid = getBrowserUuid();
      
      // Dorudan Laravel backend'e istek at - Environment-based
      const apiUrl = buildApiUrl(getApiEndpoint('CHAT'));
      

      
      const response = await fetch(apiUrl, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'X-Browser-UUID': browserUuid,
          'X-Project-ID': options.projectId?.toString() || '1401'
        },
        body: JSON.stringify({
          message: userMessage,
          project_id: options.projectId || '1401',
          browser_uuid: browserUuid,
          context: context
        })
      });



      const data = await response.json();
      
      // Debug log
      console.log('API Response:', data);
      
      // API'den error response geldiinde de ile (success: false)
      if (!response.ok || data.success === false) {
        // Token hatalarn zel olarak ile
        if (data.error && ['NO_ACTIVE_PLAN', 'USAGE_TOKENS_EXPIRED', 'INSUFFICIENT_TOKENS', 'TOKEN_VALIDATION_ERROR'].includes(data.error)) {
          const errorMessage = apiService.handleAPIError(data);
          const errorResponse: AIResponse = {
            type: 'token_error',
            message: errorMessage,
            products: [],
            intent: 'error',
            confidence: 0.0,
            suggestions: ['Plan satn al', 'Token satn al', 'Yardm al'],
            session_id: data.session_id || browserUuid,
            data: {
              error: data.error,
              redirect: data.redirect,
              tokens_remaining: data.tokens_remaining,
              tokens_used: data.tokens_used,
              tokens_total: data.tokens_total,
              usage_percentage: data.usage_percentage,
              reset_date: data.reset_date
            }
          };
          
          return errorResponse;
        }

        // Dier hatalar iin genel error handling
        const errorResponse: AIResponse = {
          type: data.type || 'error',
          message: data.message || 'Bir hata olutu',
          products: data.data?.products || data.products || [],
          intent: data.intent || data.type || 'error',
          confidence: data.confidence || 0.0,
          suggestions: [],
          session_id: data.session_id || browserUuid,
          data: data.data || data
        };
        
        return errorResponse;
      }

      // Transform API response to AIResponse format
      const aiResponse: AIResponse = {
        type: data.type || 'general',
        message: data.message || data.data?.message || 'zgnm, u anda yant veremiyorum.',
        products: data.data?.products || data.products || [],
        intent: data.intent || data.type || 'general',
        confidence: data.confidence || 0.8,
        suggestions: [], // API'den gelen suggestions kaldrld
        session_id: data.session_id || browserUuid,
        data: data.data || {}
      };
      
      // Response validation - eer message bo ise fallback kullan
      if (!aiResponse.message || aiResponse.message.trim() === '') {
        aiResponse.message = 'zgnm, u anda yant veremiyorum.';
      }

      // Products field'n kontrol et
      if (aiResponse.products && aiResponse.products.length > 0) {
        // Products varsa ve type product_search veya category_browse ise product_recommendation yap
        if (aiResponse.type === 'product_search' || aiResponse.type === 'category_browse') {
          aiResponse.type = 'product_recommendation';
        }
      }
      
      // Response validation - products varsa type' product_recommendation yap
      if (aiResponse.products && aiResponse.products.length > 0 && 
          (aiResponse.type === 'product_search' || aiResponse.type === 'category_browse')) {
        aiResponse.type = 'product_recommendation';
      }
      
      return aiResponse;
      
    } catch (error) {
      // AI Service Error - hata widget'n gster
      const apiError = errorService.handleAPIError(error, 'AI Service');
      setError(apiError.message);
      
      // Fallback to local response if API fails
      return generateLocalResponse(userMessage, context);
    } finally {
      setIsLoading(false);
    }
  }, [getBrowserUuid, generateLocalResponse, options.projectId, apiService]);

  // parseAIResponse fonksiyonu artk kullanlmyor, kaldrld

  // Laravel API call - generateAIResponse ile ayn mantk
  const generateResponse = useCallback(async (userMessage: string, sessionId?: string): Promise<AIResponse> => {
    return generateAIResponse(userMessage, { sessionId });
  }, [generateAIResponse]);

  // Event handling iin yeni fonksiyonlar
  const sendFeedback = useCallback(async (feedbackData: any) => {
    try {
      const response = await fetch(buildApiUrl(getApiEndpoint('FEEDBACK')), {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
        },
        body: JSON.stringify(feedbackData)
      });

      if (!response.ok) {
        throw new Error(`Feedback API Error: ${response.status}`);
      }

      return await response.json();
    } catch (err) {
      errorService.handleAPIError(err, 'Feedback Service');
      setError('Feedback gnderilemedi');
    }
  }, []);

  const sendProductClick = useCallback(async (productData: any) => {
    try {
      const response = await fetch(buildApiUrl(getApiEndpoint('PRODUCT_CLICK')), {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
        },
        body: JSON.stringify(productData)
      });

      if (!response.ok) {
        throw new Error(`Product Click API Error: ${response.status}`);
      }

      return await response.json();
    } catch (err) {
      errorService.handleAPIError(err, 'Product Click Service');
      setError('rn tklama kaydedilemedi');
    }
  }, []);

  const sendCargoTracking = useCallback(async (cargoData: any) => {
    try {
      const response = await fetch(buildApiUrl(getApiEndpoint('CARGO_TRACKING')), {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
        },
        body: JSON.stringify(cargoData)
      });

      if (!response.ok) {
        throw new Error(`Cargo Tracking API Error: ${response.status}`);
      }

      return await response.json();
    } catch (err) {
      errorService.handleAPIError(err, 'Cargo Tracking Service');
      setError('Kargo takip bilgisi alnamad');
    }
  }, []);

  // Token durumunu kontrol et
  const checkTokenStatus = useCallback(async () => {
    try {
      return await apiService.checkTokenStatus();
    } catch (error) {
      errorService.handleAPIError(error, 'Token Status Check');
      setError('Token durumu kontrol edilemedi');
      return { canUse: false, error: 'Token durumu kontrol edilemedi' };
    }
  }, [apiService]);

  // Kullanm bilgilerini getir
  const getUsage = useCallback(async () => {
    try {
      return await apiService.getUsage();
    } catch (error) {
      errorService.handleAPIError(error, 'Usage Information');
      setError('Kullanm bilgileri alnamad');
      return { success: false, error: 'Kullanm bilgileri alnamad' };
    }
  }, [apiService]);

  // Plan listesini getir
  const getPlans = useCallback(async () => {
    try {
      return await apiService.getPlans();
    } catch (error) {
      errorService.handleAPIError(error, 'Plans List');
      setError('Plan listesi alnamad');
      return { success: false, error: 'Plan listesi alnamad' };
    }
  }, [apiService]);

  return {
    generateResponse,
    generateAIResponse, // New API-based function
    sendFeedback,
    sendProductClick,
    sendCargoTracking,
    // Token management methods
    checkTokenStatus,
    getUsage,
    getPlans,
    isLoading,
    error,
    clearError: () => setError(null)
  };
};
