import React, { useState, useEffect, useCallback } from 'react';
import { ChatContainerProps, WidgetCustomization, Message } from '../../types';
import { getAILogoUrls } from '../../utils/imageUtils';
import MessageList from './MessageList';
import InputArea from './InputArea';
import ActionButtons from './ActionButtons';
import ChatFooter from './ChatFooter';
import CampaignTab from './CampaignTab';
import FAQTab from './FAQTab';
import NotificationWidget from '../NotificationWidget';
import APIErrorMessage from './templates/APIErrorMessage/APIErrorMessage';

import { useChat } from '../../hooks/useChat';
import { buildApiUrl } from '../../config/api';
import { errorService } from '../../services/ErrorService';
import './ChatContainer.css';

const ChatContainer: React.FC<ChatContainerProps> = ({ 
  config, 
  onEvent, 
  onFeedback, 
  onToggleChat
}) => {
  const [isSoundEnabled, setIsSoundEnabled] = useState(true);
  const [showCampaignTab, setShowCampaignTab] = useState(false);
  const [showFAQTab, setShowFAQTab] = useState(false);
  const [isTabTransitioning, setIsTabTransitioning] = useState(false);
  const [isChatVisible, setIsChatVisible] = useState(false);
  const [showNotificationWidget, setShowNotificationWidget] = useState(false);
  const [notificationSettings, setNotificationSettings] = useState<any>(null);
  const [tabAvailability, setTabAvailability] = useState({
    campaigns: false,
    faqs: false,
    loading: true
  });
  const [apiErrorMessages, setApiErrorMessages] = useState<Message[]>([]);
  const [widgetCustomization, setWidgetCustomization] = useState<WidgetCustomization>({
    ai_name: 'ConvState AI',
    welcome_message: 'Merhaba ben Kadir, senin dijital asistannm. Sana nasl yardmc olabilirim?',
    ai_personality: 'friendly',
    theme_color: '#3B82F6',
    font_family: 'Inter',
    primary_color: '#3B82F6',
    secondary_color: '#6B7280',
    language: 'tr',
    rate_limit_per_minute: 10,
    api_timeout_seconds: 10,
    max_retry_attempts: 2,
    enable_typing_indicator: true,
    enable_sound_notifications: false
  });
  
  const { 
    messages, 
    sendMessage, 
    handleProductClick, 
    handleFeedback,
    addMessage
  } = useChat(undefined, config?.projectId);

  // Apply dynamic theming based on customization
  const applyDynamicTheming = useCallback(() => {
    if (typeof document === 'undefined') return;

    const root = document.documentElement;
    
    // Apply color variables
    root.style.setProperty('--widget-primary-color', widgetCustomization.primary_color);
    root.style.setProperty('--widget-secondary-color', widgetCustomization.secondary_color);
    root.style.setProperty('--widget-theme-color', widgetCustomization.theme_color);
    
    // Apply font family
    root.style.setProperty('--widget-font-family', widgetCustomization.font_family);
    
    // Apply logo if available
    if (widgetCustomization.logo_url) {
      root.style.setProperty('--widget-logo-url', `url(${widgetCustomization.logo_url})`);
    }
  }, [widgetCustomization]);

  // Apply dynamic theming when customization changes
  useEffect(() => {
    applyDynamicTheming();
  }, [applyDynamicTheming]);

  // API hata yakalama sistemi
  useEffect(() => {
    const unsubscribe = errorService.onError((apiError) => {
      const errorMessage = errorService.createErrorMessage(apiError);
      setApiErrorMessages(prev => [...prev, errorMessage]);
    });

    return unsubscribe;
  }, []);

  const checkTabAvailability = useCallback(async () => {
    try {
      const apiUrl = buildApiUrl('/api');
      const projectId = config?.projectId || '1';
      
      // Paralel olarak kampanyalar ve SSS durumunu kontrol et
      const [campaignsResponse, faqsResponse] = await Promise.allSettled([
        fetch(`${apiUrl}/campaigns/check-availability?project_id=${projectId}`, {
          method: 'GET',
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
          }
        }),
        fetch(`${apiUrl}/faqs/check-availability?project_id=${projectId}`, {
          method: 'GET',
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
          }
        })
      ]);

      let campaignsAvailable = false;
      let faqsAvailable = false;

      // Kampanyalar durumunu kontrol et
      if (campaignsResponse.status === 'fulfilled' && campaignsResponse.value.ok) {
        const campaignsData = await campaignsResponse.value.json();
        campaignsAvailable = campaignsData.success && campaignsData.has_campaigns;
      }

      // SSS durumunu kontrol et
      if (faqsResponse.status === 'fulfilled' && faqsResponse.value.ok) {
        const faqsData = await faqsResponse.value.json();
        faqsAvailable = faqsData.success && faqsData.has_faqs;
      }

      setTabAvailability({
        campaigns: campaignsAvailable,
        faqs: faqsAvailable,
        loading: false
      });

      // Tab availability checked

    } catch (error) {
      // Error checking tab availability
      setTabAvailability({
        campaigns: false,
        faqs: false,
        loading: false
      });
    }
  }, []);

  const loadWidgetCustomization = async () => {
    try {
      // Get config from window or use defaults
      const config = (typeof window !== 'undefined' && (window as any).WIDGET_CONFIG) 
        ? (window as any).WIDGET_CONFIG 
        : {
            DEFAULT_AI_NAME: 'ConvState AI',
            DEFAULT_WELCOME_MESSAGE: 'Merhaba! Size nasl yardmc olabilirim?'
          };
      
      // Always try to load from API first
      try {
        // Get projectId and customizationToken from window config
        const projectId = (window as any).convstateaiConfig?.projectId || config?.projectId || '2';
        const customizationToken = (window as any).convstateaiConfig?.customizationToken || config?.customizationToken;
        
        if (!customizationToken) {
          throw new Error('Customization token not found');
        }
        
        const apiUrl = buildApiUrl(`/api/widget-customization?projectId=${projectId}&customizationToken=${customizationToken}`);
        // Widget Customization API URL
        
        const response = await fetch(apiUrl, {
          method: 'GET',
          headers: {
            'Content-Type': 'application/json',
            'Accept': 'application/json'
          }
        });
        
        // API Response Status
        
        if (response.ok) {
          const data = await response.json();
          // API Response Data
          
          if (data.success && data.data) {
            const customization = data.data.widgetCustomization || data.data;
            const apiEndpoints = data.data.apiEndpoints || {};
            const isCargoEnabled = data.data.isCargoEnabled || false;
            const isOrderEnabled = data.data.isOrderEnabled || false;
            
            // Check if customization_data exists and is not null
            const customizationData = customization.customization_data;
            
            // Default theme values
            const defaultTheme = {
              ai_personality: 'friendly',
              theme_color: '#3B82F6',
              font_family: 'Inter',
              primary_color: '#3B82F6',
              secondary_color: '#6B7280',
              language: 'tr',
              rate_limit_per_minute: 10,
              api_timeout_seconds: 10,
              max_retry_attempts: 2,
              enable_typing_indicator: true,
              enable_sound_notifications: false
            };
            
            // Use customization_data if available, otherwise use default theme
            const themeSettings = customizationData && typeof customizationData === 'object' 
              ? { ...defaultTheme, ...customizationData }
              : defaultTheme;
            
            // Convstateai balatlyor - API balantlar baarl
            console.log('Convstateai balatlyor');
            
            setWidgetCustomization({
              ai_name: customization.ai_name || config.DEFAULT_AI_NAME,
              welcome_message: customization.welcome_message || config.DEFAULT_WELCOME_MESSAGE,
              ai_personality: themeSettings.ai_personality,
              theme_color: customization.theme_color || themeSettings.theme_color,
              font_family: customization.font_family || themeSettings.font_family,
              primary_color: customization.primary_color || themeSettings.primary_color,
              secondary_color: customization.secondary_color || themeSettings.secondary_color,
              language: customization.language || themeSettings.language,
              rate_limit_per_minute: themeSettings.rate_limit_per_minute,
              api_timeout_seconds: themeSettings.api_timeout_seconds,
              max_retry_attempts: themeSettings.max_retry_attempts,
              enable_typing_indicator: customization.enable_typing_indicator !== undefined ? customization.enable_typing_indicator : themeSettings.enable_typing_indicator,
              enable_sound_notifications: customization.enable_sound_notifications !== undefined ? customization.enable_sound_notifications : themeSettings.enable_sound_notifications,
              cargo_not_found_message: customization.cargo_not_found_message,
              feature_disabled_message: customization.feature_disabled_message,
              error_message_template: customization.error_message_template,
              order_not_found_message: customization.order_not_found_message,
              logo_url: customization.logo_url,
              custom_messages: customization.custom_messages
            });
            
            // Update global widget config with API endpoints
            if (typeof window !== 'undefined') {
              (window as any).widgetConfig = {
                ...(window as any).widgetConfig,
                apiEndpoints: {
                  cargoTracking: apiEndpoints.cargoTracking || null,
                  orderTracking: apiEndpoints.orderTracking || null
                },
                isCargoEnabled: isCargoEnabled,
                isOrderEnabled: isOrderEnabled
              };
            }
            return;
          } else {
            // API Response not successful
          }
        } else {
          // API Response Error
        }
      } catch (apiError) {
        // API Error
        // API not available, using default values
      }
      
      // Use default values from config (fallback)
      setWidgetCustomization({
        ai_name: config.DEFAULT_AI_NAME,
        welcome_message: `Merhaba ben ${config.DEFAULT_AI_NAME}, sizin tercihlerinize gre rn nerileri sunuyorum. Size nasl yardmc olabilirim?`,
        ai_personality: 'friendly',
        theme_color: '#3B82F6',
        font_family: 'Inter',
        primary_color: '#3B82F6',
        secondary_color: '#6B7280',
        language: 'tr',
        rate_limit_per_minute: 10,
        api_timeout_seconds: 10,
        max_retry_attempts: 2,
        enable_typing_indicator: true,
        enable_sound_notifications: false
      });
    } catch (error) {
      // Widget customization error
      // API'den veri alnamadnda minimal default deerler kullan
      setWidgetCustomization({
        ai_name: 'ConvState AI',
        welcome_message: 'Merhaba ben ConvState AI, sizin tercihlerinize gre rn nerileri sunuyorum. Size nasl yardmc olabilirim?',
        ai_personality: 'friendly',
        theme_color: '#3B82F6',
        font_family: 'Inter',
        primary_color: '#3B82F6',
        secondary_color: '#6B7280',
        language: 'tr',
        rate_limit_per_minute: 10,
        api_timeout_seconds: 10,
        max_retry_attempts: 2,
        enable_typing_indicator: true,
        enable_sound_notifications: false
      });
    }
  };

  const loadNotificationWidget = useCallback(async () => {
    try {
      // Show notification on every page load (removed localStorage check)
      //console.log(' Loading notification widget...');

      const apiUrl = buildApiUrl('/api/notification-widget');
      const projectId = config?.projectId || '1';
      
      //console.log(' API URL:', `${apiUrl}/check-availability?project_id=${projectId}`);
      
      const response = await fetch(`${apiUrl}/check-availability?project_id=${projectId}`, {
        method: 'GET',
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        }
      });

      //console.log(' API Response status:', response.status);

      if (response.ok) {
        const data = await response.json();
        //console.log(' API Response data:', data);
        
        if (data.success && data.has_notification) {
          //console.log(' Notification available, showing widget');
          setNotificationSettings(data.data);
          setShowNotificationWidget(true);
          
          // Track event
          onEvent?.({
            type: 'message_sent',
            timestamp: new Date(),
            sessionId: 'session_id',
            data: { action: 'notification_shown', message: data.data.message_text }
          });
        } else {
          //console.log(' No notification available from server, showing default notification');
          
          // Default notification when server returns false
          setNotificationSettings({
            message_text: 'Sizin iin rn seebilirim',
            ai_name: 'ConvState AI',
            color_theme: 'blue',
            display_duration: 5000,
            animation_type: 'slide-in',
            show_close_button: true,
            color_theme_css: {
              primary: '#3B82F6',
              secondary: '#1D4ED8',
              gradient: 'linear-gradient(135deg, #3B82F6 0%, #1D4ED8 100%)'
            }
          });
          setShowNotificationWidget(true);
        }
      } else {
        //console.log(' API Response not OK:', response.status);
      }
    } catch (error) {
      // Notification widget error - fail silently
      // Failed to load notification widget
    }
  }, [config?.projectId, onEvent]);

  // Load widget customization on component mount
  useEffect(() => {
    loadWidgetCustomization();
    checkTabAvailability();
    loadNotificationWidget();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []); // Empty dependency array - run only once on mount

  const handleNotificationClose = () => {
    setShowNotificationWidget(false);
    onEvent?.({
      type: 'message_sent',
      timestamp: new Date(),
      sessionId: 'session_id',
      data: { action: 'notification_closed' }
    });
  };

  // Kampanya ile ilgili mesajlar tespit et ve direkt tab 2'ye ge
  useEffect(() => {
    const lastMessage = messages[messages.length - 1];
    if (lastMessage && lastMessage.role === 'user') {
      const message = lastMessage.content.toLowerCase();
      const campaignKeywords = ['kampanya', 'kampanyalar', 'kampanyalarda', 'indirim', 'frsat', 'bedava', 'cretsiz', 'taksit', 'promosyon', 'teklif', 'zel', 'avantaj'];
      
      // zel durum: "kampanyalarda neler var" sorusu iin tab navigation gster ama otomatik gei yapma
      if (message.includes('kampanyalarda neler var')) {
        onEvent?.({
          type: 'message_sent',
          timestamp: new Date(),
          sessionId: 'session_id',
          data: { action: 'campaign_inquiry', message }
        });
        return; // Otomatik tab geii yapma
      }
      
      const hasCampaignKeyword = campaignKeywords.some(keyword => 
        message.includes(keyword)
      );
      
      if (hasCampaignKeyword) {
        setShowCampaignTab(true);
        
        // Kampanya mesajn ekleme - direkt tab deiimi
        onEvent?.({
          type: 'message_sent',
          timestamp: new Date(),
          sessionId: 'session_id',
          data: { action: 'campaign_tab_opened', message }
        });
      }
    }
  }, [messages, onEvent]);

  const handleSoundToggle = () => {
    setIsSoundEnabled(!isSoundEnabled);
    onEvent?.({
      type: 'message_sent',
      timestamp: new Date(),
      sessionId: 'session_id',
      data: { action: 'sound_toggle', enabled: !isSoundEnabled }
    });
  };

  const handleSendMessage = (message: string) => {
    sendMessage(message);
    onEvent?.({
      type: 'message_sent',
      timestamp: new Date(),
      sessionId: 'session_id',
      data: { message }
    });
  };

  const handleProductClickEvent = (product: any) => {
    handleProductClick(product);
    onEvent?.({
      type: 'product_clicked',
      timestamp: new Date(),
      sessionId: 'session_id',
      data: { product }
    });
  };

  const handleFeedbackEvent = (feedback: any) => {
    handleFeedback(feedback);
    onEvent?.({
      type: 'feedback_received',
      timestamp: new Date(),
      sessionId: 'session_id',
      data: { feedback }
    });
  };

  const handleActionClick = (action: string) => {
    // Send action as a message
    sendMessage(action);
    onEvent?.({
      type: 'message_sent',
      timestamp: new Date(),
      sessionId: 'session_id',
      data: { action }
    });
  };

  const handleCloseCampaignTab = () => {
    setShowCampaignTab(false);
  };

  const handleBottomAvatarClick = () => {
    // Close notification widget if it's visible
    if (showNotificationWidget) {
      setShowNotificationWidget(false);
      onEvent?.({
        type: 'message_sent',
        timestamp: new Date(),
        sessionId: 'session_id',
        data: { action: 'notification_closed_via_avatar' }
      });
    }
    
    setIsChatVisible(!isChatVisible);
    onEvent?.({
      type: 'message_sent',
      timestamp: new Date(),
      sessionId: 'session_id',
      data: { action: 'chat_toggle', visible: !isChatVisible }
    });
  };



  const handleTabChange = (tabName: string) => {
    // Check if the tab is available before switching
    if (tabName === 'campaign' && !tabAvailability.campaigns) {
      // Campaign tab is not available
      return;
    }
    
    if (tabName === 'faq' && !tabAvailability.faqs) {
      // FAQ tab is not available
      return;
    }

    setIsTabTransitioning(true);
    
    // Smooth transition iin ksa delay
    setTimeout(() => {
      // Reset all tabs first
      setShowCampaignTab(false);
      setShowFAQTab(false);
      
      // Show selected tab
      if (tabName === 'campaign' && tabAvailability.campaigns) {
        setShowCampaignTab(true);
      } else if (tabName === 'faq' && tabAvailability.faqs) {
        setShowFAQTab(true);
      }
      // chat tab is default (both false)
      
      // Transition tamamlandktan sonra state'i sfrla
      setTimeout(() => {
        setIsTabTransitioning(false);
      }, 400); // CSS transition sresi ile eletir
    }, 100);
  };

  return (
    <>
      {/* Main Chat Container */}
      <div className={`convstateai-widget chat-container ${!isChatVisible ? 'hidden' : ''} ${(config as any)?.customization?.position === 'center' ? 'center' : ''}`}>
        {/* Main Header Container - Combined Header and Tabs */}
        <div className="main-header">
          {/* Header */}
          <div className="chat-header">
            <div className="header-left">
              <div className="agent-avatar">
                <img src={getAILogoUrls().small} alt="AI Conversion Agent" />
              </div>
              <span className="agent-name">{widgetCustomization.ai_name || 'ConvState AI'}</span>
            </div>
            <div className="header-right">
              {/* Sound Toggle Button */}
              <button 
                className={`icon-button ${isSoundEnabled ? 'sound-enabled' : ''}`}
                onClick={handleSoundToggle}
                title={isSoundEnabled ? "Sound On" : "Sound Off"}
              >
                <svg width="24" height="24" viewBox="0 0 24 24" fill="none">
                  <path d="M12 5L8 9H4V15H8L12 19V5Z" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
                  <path d="M15.54 8.46C16.4774 9.39764 17.0039 10.6692 17.0039 12C17.0039 13.3308 16.4774 14.6024 15.54 15.54" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
                </svg>
              </button>
            </div>
          </div>

          {/* Tabs Section - Only show if there are multiple tabs available */}
          {(tabAvailability.campaigns || tabAvailability.faqs) && (
            <div className="tabs-section">
              <div className="tab-navigation">
                {/* Chat Tab - Only show if other tabs are available */}
                <button 
                  className={`tab-button ${!showCampaignTab && !showFAQTab ? 'active' : ''}`}
                  onClick={() => handleTabChange('chat')}
                  title="Chat"
                >
                   Chat
                </button>
                
                {/* Campaign Tab - Only show if campaigns are available */}
                {tabAvailability.campaigns && (
                  <button 
                    className={`tab-button ${showCampaignTab ? 'active' : ''}`}
                    onClick={() => handleTabChange('campaign')}
                    title="Kampanyalar"
                  >
                     Kampanyalar
                  </button>
                )}
                
                {/* FAQ Tab - Only show if FAQs are available */}
                {tabAvailability.faqs && (
                  <button 
                    className={`tab-button ${showFAQTab ? 'active' : ''}`}
                    onClick={() => handleTabChange('faq')}
                    title="Sk Sorulan Sorular"
                  >
                     SSS
                  </button>
                )}
              </div>
            </div>
          )}
        </div>

        {/* Tab Content */}
        <div className={`tab-content ${isTabTransitioning ? 'transitioning' : ''}`}>
          {/* Chat Tab */}
          <div className={`tab-panel ${!showCampaignTab ? 'active' : ''}`}>
            <div className="aiagent-container">
              {/* Content */}
              <div className="chat-content">
                {/* API Error Messages */}
                {apiErrorMessages.map((errorMessage) => (
                  <APIErrorMessage
                    key={errorMessage.id}
                    message={errorMessage}
                    onRetry={() => {
                      // Hata mesajn kaldr ve tekrar dene
                      setApiErrorMessages(prev => 
                        prev.filter(msg => msg.id !== errorMessage.id)
                      );
                      // Gerekirse retry logic'i buraya eklenebilir
                    }}
                    onDismiss={() => {
                      setApiErrorMessages(prev => 
                        prev.filter(msg => msg.id !== errorMessage.id)
                      );
                    }}
                  />
                ))}
                
                <MessageList 
                  messages={messages}
                  onProductClick={handleProductClickEvent} 
                  onFeedback={handleFeedbackEvent}
                  welcomeMessage={widgetCustomization.welcome_message}
                  widgetCustomization={widgetCustomization}
                  onAddMessage={addMessage}
                  onSendMessage={sendMessage}
                />
              </div>
              
              {/* Action Buttons */}
              <ActionButtons 
                onAction={handleActionClick} 
              />

              {/* Input Area */}
              <InputArea 
                onSendMessage={handleSendMessage} 
              />

              {/* Footer */}
              <ChatFooter />
            </div>
          </div>

          {/* Campaign Tab - Only render if campaigns are available */}
          {tabAvailability.campaigns && (
            <div className={`tab-panel ${showCampaignTab ? 'active' : ''}`}>
              <CampaignTab 
                isVisible={true}
                onClose={handleCloseCampaignTab}
              />
            </div>
          )}

          {/* FAQ Tab - Only render if FAQs are available */}
          {tabAvailability.faqs && (
            <div className={`tab-panel ${showFAQTab ? 'active' : ''}`}>
              <FAQTab 
                isVisible={true}
                onClose={() => handleTabChange('chat')}
              />
            </div>
          )}
        </div>
      </div>

      {/* Bottom Right Avatar */}
      <div className="bottom-avatar" onClick={handleBottomAvatarClick}>
        <img 
          src={getAILogoUrls().large} 
          alt="AI Conversion Assistant" 
        />
      </div>

      {/* Notification Widget */}
      <NotificationWidget
        isVisible={showNotificationWidget}
        onClose={handleNotificationClose}
        onOpenChat={handleBottomAvatarClick}
        settings={notificationSettings}
      />
    </>
  );
};

export default ChatContainer;
