import React, { useState, useEffect } from 'react';
import { Message } from '../../../../types';
import { APIError, ErrorWidgetConfig } from '../../../../services/ErrorService';
import './APIErrorMessage.css';

interface APIErrorMessageProps {
  message: Message;
  onRetry?: () => void;
  onDismiss?: () => void;
}

const APIErrorMessage: React.FC<APIErrorMessageProps> = ({ 
  message, 
  onRetry, 
  onDismiss 
}) => {
  const [isVisible, setIsVisible] = useState(true);
  const [showDetails, setShowDetails] = useState(false);
  
  const errorData = message.data || {};
  const apiError: APIError = {
    type: errorData.error?.toLowerCase() || 'unknown',
    status: errorData.status,
    message: message.message || message.content || 'Unknown error',
    details: errorData.details,
    timestamp: new Date(message.timestamp).getTime()
  };
  
  const config: ErrorWidgetConfig = errorData.config || {
    showRetry: true,
    showDetails: false,
    autoHide: true,
    hideDelay: 5000
  };

  // Auto-hide functionality
  useEffect(() => {
    if (config.autoHide && config.hideDelay > 0) {
      const timer = setTimeout(() => {
        setIsVisible(false);
        onDismiss?.();
      }, config.hideDelay);

      return () => clearTimeout(timer);
    }
  }, [config.autoHide, config.hideDelay, onDismiss]);

  const getErrorIcon = () => {
    switch (apiError.type) {
      case 'network':
        return '';
      case 'server':
        return '';
      case 'client':
        return '';
      case 'timeout':
        return '';
      default:
        return '';
    }
  };

  const getErrorTitle = () => {
    switch (apiError.type) {
      case 'network':
        return 'Balant Hatas';
      case 'server':
        return 'Sunucu Hatas';
      case 'client':
        return 'stek Hatas';
      case 'timeout':
        return 'Zaman Am';
      default:
        return 'Hata';
    }
  };

  const getErrorColor = () => {
    switch (apiError.type) {
      case 'network':
        return '#3B82F6'; // Blue
      case 'server':
        return '#EF4444'; // Red
      case 'client':
        return '#F59E0B'; // Yellow
      case 'timeout':
        return '#8B5CF6'; // Purple
      default:
        return '#6B7280'; // Gray
    }
  };

  const handleRetry = () => {
    onRetry?.();
    setIsVisible(false);
  };

  const handleDismiss = () => {
    setIsVisible(false);
    onDismiss?.();
  };

  const handleRedirect = () => {
    if (config.redirectUrl) {
      window.location.href = config.redirectUrl;
    }
  };

  if (!isVisible) {
    return null;
  }

  return (
    <div 
      className="api-error-message"
      style={{ borderLeftColor: getErrorColor() }}
    >
      <div className="api-error-header">
        <div className="api-error-icon" style={{ color: getErrorColor() }}>
          {getErrorIcon()}
        </div>
        <div className="api-error-content">
          <h3 className="api-error-title">{getErrorTitle()}</h3>
          <p className="api-error-description">{apiError.message}</p>
          
          {apiError.status && (
            <div className="api-error-status">
              <span className="status-label">Durum Kodu:</span>
              <span className="status-code">{apiError.status}</span>
            </div>
          )}
        </div>
        
        <div className="api-error-actions">
          {config.showDetails && apiError.details && (
            <button
              className="api-error-details-btn"
              onClick={() => setShowDetails(!showDetails)}
              title="Detaylar gster/gizle"
            >
              {showDetails ? '' : ''}
            </button>
          )}
          
          <button
            className="api-error-close-btn"
            onClick={handleDismiss}
            title="Kapat"
          >
            
          </button>
        </div>
      </div>

      {showDetails && apiError.details && (
        <div className="api-error-details">
          <h4>Hata Detaylar:</h4>
          <pre className="api-error-details-content">
            {JSON.stringify(apiError.details, null, 2)}
          </pre>
        </div>
      )}

      <div className="api-error-footer">
        {config.showRetry && (
          <button
            className="api-error-retry-btn"
            onClick={handleRetry}
          >
             Tekrar Dene
          </button>
        )}
        
        {config.redirectUrl && (
          <button
            className="api-error-redirect-btn"
            onClick={handleRedirect}
          >
             {config.redirectUrl.includes('login') ? 'Giri Yap' : 'Ynlendir'}
          </button>
        )}
        
        {config.autoHide && (
          <div className="api-error-auto-hide">
            <span>Bu mesaj {Math.ceil(config.hideDelay / 1000)} saniye sonra kapanacak</span>
          </div>
        )}
      </div>
    </div>
  );
};

export default APIErrorMessage;
