/**
 * API Configuration Service
 * Environment-based API URL ynetimi
 * Mevcut config.js yaps ile uyumlu
 */

export interface APIConfig {
  BASE_URL: string;
  TIMEOUT: number;
  ENDPOINTS: {
    CHAT: string;
    CAMPAIGNS: string;
    FAQS: string;
    PRODUCTS: string;
    WIDGET_CUSTOMIZATION: string;
    FEEDBACK: string;
    PRODUCT_CLICK: string;
    CARGO_TRACKING: string;
    // Token Management Endpoints
    ADD_TOKEN: string;
    EXHAUST_TOKEN: string;
    // Subscription Endpoints
    USAGE: string;
    PLANS: string;
    BILLING_HISTORY: string;
  };
  DEFAULT_AI_NAME: string;
  DEFAULT_WELCOME_MESSAGE: string;
  SESSION_STORAGE_KEY: string;
  MAX_PRODUCTS_TO_SHOW: number;
  PRODUCT_IMAGE_PATH: string;
  TYPING_DELAY: number;
  MAX_MESSAGE_LENGTH: number;
  ENABLE_TTS: boolean;
  ENABLE_PRODUCT_RECOMMENDATIONS: boolean;
  ENABLE_CAMPAIGN_TAB: boolean;
  ENABLE_FAQ_TAB: boolean;
}

// Environment-based configuration
const getAPIConfig = (): APIConfig => {
  // Environment deikeni varsa onu kullan, yoksa environment'a gre default deer kullan
  let baseUrl: string;
  const env = process.env.REACT_APP_ENV || process.env.NODE_ENV || 'production';
  
  if (env === 'development') {
    baseUrl = process.env.REACT_APP_API_BASE_URL || 'http://127.0.0.1:8000';
  } else {
    baseUrl = process.env.REACT_APP_API_BASE_URL || 'https://convstateai.com';
  }
  
  return {
    BASE_URL: baseUrl,
    TIMEOUT: parseInt(process.env.REACT_APP_API_TIMEOUT || '10000'),
    ENDPOINTS: {
      CHAT: '/api/chat',
      CAMPAIGNS: '/api/campaigns',
      FAQS: '/api/faqs',
      PRODUCTS: '/api/products',
      WIDGET_CUSTOMIZATION: '/api/widget-customization',
      FEEDBACK: '/api/feedback',
      PRODUCT_CLICK: '/api/product-click',
      CARGO_TRACKING: '/api/cargo-tracking',
      // Token Management Endpoints
      ADD_TOKEN: '/api/add-token',
      EXHAUST_TOKEN: '/api/exhaust-token',
      // Subscription Endpoints
      USAGE: '/api/subscription/usage',
      PLANS: '/api/subscription/plans',
      BILLING_HISTORY: '/api/subscription/billing-history'
    },
    DEFAULT_AI_NAME: process.env.REACT_APP_DEFAULT_AI_NAME || 'ConvState AI',
    DEFAULT_WELCOME_MESSAGE: process.env.REACT_APP_DEFAULT_WELCOME_MESSAGE || 'Merhaba ben Kadir, senin dijital asistannm. Sana nasl yardmc olabilirim?',
    SESSION_STORAGE_KEY: 'chat_session_id',
    MAX_PRODUCTS_TO_SHOW: 6,
    PRODUCT_IMAGE_PATH: baseUrl + '/api/widgetcust/imgs/',
    TYPING_DELAY: 1000,
    MAX_MESSAGE_LENGTH: 1000,
    ENABLE_TTS: process.env.REACT_APP_ENABLE_TTS === 'true',
    ENABLE_PRODUCT_RECOMMENDATIONS: process.env.REACT_APP_ENABLE_PRODUCT_RECOMMENDATIONS === 'true',
    ENABLE_CAMPAIGN_TAB: process.env.REACT_APP_ENABLE_CAMPAIGN_TAB === 'true',
    ENABLE_FAQ_TAB: process.env.REACT_APP_ENABLE_FAQ_TAB === 'true'
  };
};

// Global config instance
export const API_CONFIG = getAPIConfig();

// Helper functions - mevcut config.js ile uyumlu
export const getConfigValue = (key: keyof APIConfig): any => {
  return API_CONFIG[key];
};

export const buildApiUrl = (endpoint: string): string => {
  return API_CONFIG.BASE_URL + endpoint;
};

export const getApiEndpoint = (endpoint: keyof APIConfig['ENDPOINTS']): string => {
  return API_CONFIG.ENDPOINTS[endpoint];
};

