import React, { useState, useEffect } from 'react';
import './chatbot.css';
import { buildApiUrl } from '../../config/api';

interface CampaignTabProps {
  isVisible: boolean;
  onClose: () => void;
}

interface Campaign {
  id: number;
  title: string;
  description: string;
  category: string;
  discount: string;
  valid_until: string;
  start_date: string;
  end_date: string;
  discount_type: string;
  discount_value: number;
  minimum_order_amount: number;
  is_active: boolean;
  image_url?: string;
}

const CampaignTab: React.FC<CampaignTabProps> = ({ isVisible, onClose }) => {
  const [campaigns, setCampaigns] = useState<Campaign[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    if (isVisible) {
      loadCampaigns();
    }
  }, [isVisible]);

  const loadCampaigns = async () => {
    try {
      setLoading(true);
      setError(null);
      
      // API endpoint'i - Laravel backend kullan
      const apiUrl = buildApiUrl('/api');
      
      // Project ID'yi global config'den al veya default olarak 1 kullan
      const projectId = (typeof window !== 'undefined' && (window as any).WIDGET_PROJECT_ID) || '1';
      const response = await fetch(`${apiUrl}/campaigns?project_id=${projectId}`, {
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        }
      });
      
      if (!response.ok) {
        if (response.status === 404) {
          // Site bulunamad veya kampanya yok - bu normal bir durum
          setCampaigns([]);
          setError(null);
          return;
        }
        // Error response text
        throw new Error(`Kampanyalar yklenirken hata olutu: ${response.status}`);
      }
      
      const responseText = await response.text();
      
      let result;
      try {
        result = JSON.parse(responseText);
      } catch (parseError) {
        // JSON parse error
        throw new Error('API response JSON formatnda deil');
      }
      
      if (result.success) {
        setCampaigns(result.data || []);
      } else {
        setError(result.message || 'Kampanyalar yklenemedi');
      }
    } catch (err) {
      // Campaign loading error
      setError(err instanceof Error ? err.message : 'Bilinmeyen hata olutu');
    } finally {
      setLoading(false);
    }
  };

  const formatDate = (dateString: string | null) => {
    if (!dateString) return 'Srekli';
    
    try {
      const date = new Date(dateString);
      return date.toLocaleDateString('tr-TR');
    } catch {
      return 'Geersiz tarih';
    }
  };

  const getDiscountDisplay = (campaign: Campaign) => {
    switch (campaign.discount_type) {
      case 'percentage':
        return `%${campaign.discount_value} ndirim`;
      case 'fixed':
        return `${campaign.discount_value} TL ndirim`;
      case 'free_shipping':
        return 'cretsiz Kargo';
      case 'buy_x_get_y':
      default:
        return campaign.discount;
    }
  };

  if (loading) {
    return (
      <div className="campaign-tab-content">
        <div className="campaign-content">
          <div className="campaign-item shimmer">
            <div className="campaign-item-header">
              <div className="h-4 bg-gray-200 rounded w-3/4 mb-2"></div>
              <div className="h-3 bg-gray-200 rounded w-1/4"></div>
            </div>
            <div className="h-3 bg-gray-200 rounded w-full mb-2"></div>
            <div className="h-3 bg-gray-200 rounded w-2/3"></div>
          </div>
          <div className="campaign-item shimmer">
            <div className="campaign-item-header">
              <div className="h-4 bg-gray-200 rounded w-2/3 mb-2"></div>
              <div className="h-3 bg-gray-200 rounded w-1/3"></div>
            </div>
            <div className="h-3 bg-gray-200 rounded w-full mb-2"></div>
            <div className="h-3 bg-gray-200 rounded w-3/4"></div>
          </div>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="campaign-tab-content">
        {/* Campaign List with Error Item */}
        <div className="campaign-content">
          <div className="campaign-item error-state">
            <div className="campaign-item-header">
              <h4 className="text-red-600"> Hata Olutu</h4>
              <span className="campaign-category error-category">Sistem Hatas</span>
            </div>
            <p className="campaign-description text-red-500">{error}</p>
            <div className="campaign-item-footer">
              <span className="campaign-discount error-discount">Yklenemedi</span>
              <span className="campaign-validity">imdi</span>
            </div>
            
            {/* Retry Button */}
            <div className="campaign-details">
              <button 
                onClick={loadCampaigns}
                className="campaign-retry-btn"
              >
                 Tekrar Dene
              </button>
            </div>
          </div>
        </div>

        {/* Footer */}
        <div className="campaign-footer">
          <button className="campaign-action-btn" onClick={onClose}>
             Chat'e Dn
          </button>
        </div>
      </div>
    );
  }


  
  if (!campaigns || campaigns.length === 0) {
    return (
      <div className="campaign-tab-content">
        <div className="campaign-content">
          <div className="campaign-item empty-state">
            <div className="text-center py-8">
              <div className="text-gray-400 text-6xl mb-4"></div>
              <h3 className="text-lg font-medium text-gray-900 mb-2">Henz Kampanya Yok</h3>
              <p className="text-gray-500">Bu site iin henz aktif kampanya bulunmuyor. Ynetici panelinden kampanya eklenebilir.</p>
            </div>
          </div>
        </div>
        
        {/* Footer */}
        <div className="campaign-footer">
          <button className="campaign-action-btn" onClick={onClose}>
             Chat'e Dn
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="campaign-tab-content">
      {/* Campaign List */}
      <div className="campaign-content">
        {campaigns.map((campaign) => (
          <div key={campaign.id} className="campaign-item">
            <div className="campaign-item-header">
              <h4>{campaign.title}</h4>
              <span className="campaign-category">{campaign.category}</span>
            </div>
            <p className="campaign-description">{campaign.description}</p>
            <div className="campaign-item-footer">
              <span className="campaign-discount">{getDiscountDisplay(campaign)}</span>
              <span className="campaign-validity">
                Geerli: {formatDate(campaign.end_date)}
              </span>
            </div>
            
            {/* Additional campaign details */}
            {campaign.minimum_order_amount && (
              <div className="campaign-details">
                <small className="text-gray-500">
                  Min. Sipari: {campaign.minimum_order_amount} TL
                </small>
              </div>
            )}
          </div>
        ))}
      </div>

      {/* Footer */}
      <div className="campaign-footer">
        <button className="campaign-action-btn" onClick={onClose}>
           Chat'e Dn
        </button>
      </div>
    </div>
  );
};

export default CampaignTab;
