import { Message } from '../types';

export interface APIError {
  type: 'network' | 'server' | 'client' | 'timeout' | 'unknown';
  status?: number;
  message: string;
  details?: any;
  timestamp: number;
}

export interface ErrorWidgetConfig {
  showRetry: boolean;
  showDetails: boolean;
  autoHide: boolean;
  hideDelay: number;
  redirectUrl?: string;
}

export class ErrorService {
  private static instance: ErrorService;
  private errorHandlers: ((error: APIError) => void)[] = [];

  private constructor() {}

  static getInstance(): ErrorService {
    if (!ErrorService.instance) {
      ErrorService.instance = new ErrorService();
    }
    return ErrorService.instance;
  }

  /**
   * Hata yakalama fonksiyonunu kaydet
   */
  onError(handler: (error: APIError) => void): () => void {
    this.errorHandlers.push(handler);
    return () => {
      const index = this.errorHandlers.indexOf(handler);
      if (index > -1) {
        this.errorHandlers.splice(index, 1);
      }
    };
  }

  /**
   * API hatasn ile ve widget' gster
   */
  handleAPIError(error: any, context?: string): APIError {
    const apiError = this.parseError(error, context);
    this.notifyError(apiError);
    return apiError;
  }

  /**
   * Hata mesajn parse et
   */
  private parseError(error: any, context?: string): APIError {
    const timestamp = Date.now();
    
    // Network hatas
    if (error instanceof TypeError && error.message.includes('fetch')) {
      return {
        type: 'network',
        message: 'Balant hatas. nternet balantnz kontrol edin.',
        details: { originalError: error.message, context },
        timestamp
      };
    }

    // HTTP status hatas
    if (error.status || (error.response && error.response.status)) {
      const status = error.status || error.response.status;
      return {
        type: 'server',
        status,
        message: this.getStatusMessage(status),
        details: { originalError: error, context },
        timestamp
      };
    }

    // Timeout hatas
    if (error.name === 'AbortError' || error.message?.includes('timeout')) {
      return {
        type: 'timeout',
        message: 'stek zaman amna urad. Ltfen tekrar deneyin.',
        details: { originalError: error, context },
        timestamp
      };
    }

    // Client hatas (4xx)
    if (error.status >= 400 && error.status < 500) {
      return {
        type: 'client',
        status: error.status,
        message: this.getClientErrorMessage(error),
        details: { originalError: error, context },
        timestamp
      };
    }

    // Bilinmeyen hata
    return {
      type: 'unknown',
      message: error.message || 'Bilinmeyen bir hata olutu.',
      details: { originalError: error, context },
      timestamp
    };
  }

  /**
   * HTTP status koduna gre mesaj dndr
   */
  private getStatusMessage(status: number): string {
    switch (status) {
      case 400:
        return 'Geersiz istek. Ltfen verilerinizi kontrol edin.';
      case 401:
        return 'Yetkilendirme hatas. Ltfen giri yapn.';
      case 403:
        return 'Bu ilem iin yetkiniz bulunmuyor.';
      case 404:
        return 'stenen kaynak bulunamad.';
      case 408:
        return 'stek zaman amna urad.';
      case 429:
        return 'ok fazla istek gnderildi. Ltfen bekleyin.';
      case 500:
        return 'Sunucu hatas. Ltfen daha sonra tekrar deneyin.';
      case 502:
        return 'Sunucu geici olarak kullanlamyor.';
      case 503:
        return 'Hizmet geici olarak kullanlamyor.';
      case 504:
        return 'Sunucu yant vermiyor.';
      default:
        return `Sunucu hatas (${status}). Ltfen daha sonra tekrar deneyin.`;
    }
  }

  /**
   * Client hata mesajn al
   */
  private getClientErrorMessage(error: any): string {
    // API'den gelen hata mesajn kontrol et
    if (error.data?.message) {
      return error.data.message;
    }
    
    if (error.message) {
      return error.message;
    }

    return this.getStatusMessage(error.status);
  }

  /**
   * Hata widget'n gster
   */
  private notifyError(error: APIError): void {
    this.errorHandlers.forEach(handler => {
      try {
        handler(error);
      } catch (e) {
        console.error('Error handler failed:', e);
      }
    });
  }

  /**
   * Hata widget' iin konfigrasyon al
   */
  getErrorWidgetConfig(error: APIError): ErrorWidgetConfig {
    switch (error.type) {
      case 'network':
        return {
          showRetry: true,
          showDetails: false,
          autoHide: false,
          hideDelay: 0
        };
      
      case 'timeout':
        return {
          showRetry: true,
          showDetails: false,
          autoHide: false,
          hideDelay: 0
        };
      
      case 'client':
        if (error.status === 401 || error.status === 403) {
          return {
            showRetry: false,
            showDetails: false,
            autoHide: false,
            hideDelay: 0,
            redirectUrl: '/login'
          };
        }
        return {
          showRetry: true,
          showDetails: true,
          autoHide: true,
          hideDelay: 5000
        };
      
      case 'server':
        return {
          showRetry: true,
          showDetails: true,
          autoHide: true,
          hideDelay: 8000
        };
      
      default:
        return {
          showRetry: true,
          showDetails: false,
          autoHide: true,
          hideDelay: 5000
        };
    }
  }

  /**
   * Hata mesajn Message formatna evir
   */
  createErrorMessage(apiError: APIError): Message {
    return {
      id: `error_${apiError.timestamp}`,
      role: 'agent',
      content: apiError.message,
      type: 'error',
      message: apiError.message,
      timestamp: new Date(apiError.timestamp),
      data: {
        error: apiError.type.toUpperCase(),
        status: apiError.status,
        details: apiError.details,
        config: this.getErrorWidgetConfig(apiError)
      }
    };
  }
}

export const errorService = ErrorService.getInstance();
