import { AuthService } from './AuthService';
import { uuidService } from './UuidService';
import { errorService } from './ErrorService';
import { 
  UsageToken, 
  Plan, 
  Subscription, 
  AddTokenResponse, 
  ExhaustTokenResponse, 
  UsageResponse, 
  PlansResponse, 
  SubscriptionResponse,
  APIError 
} from '../types';

export interface ApiResponse<T = any> {
  success: boolean;
  data?: T;
  message?: string;
  error?: string;
}

export class ApiService {
  private static instance: ApiService;
  private authService = AuthService.getInstance();
  private baseUrl: string = '';

  private constructor() {}

  static getInstance(): ApiService {
    if (!ApiService.instance) {
      ApiService.instance = new ApiService();
    }
    return ApiService.instance;
  }

  setBaseUrl(url: string): void {
    this.baseUrl = url;
  }

  private async makeRequest<T>(
    endpoint: string, 
    options: RequestInit = {}
  ): Promise<ApiResponse<T>> {
    try {
      const url = `${this.baseUrl}${endpoint}`;
      const headers = {
        ...this.authService.getAuthHeaders(),
        ...options.headers
      };

      const response = await fetch(url, {
        ...options,
        headers
      });

      if (!response.ok) {
        const errorData = {
          status: response.status,
          statusText: response.statusText,
          url: url,
          endpoint: endpoint
        };
        
        // Hata widget'n gster
        errorService.handleAPIError(errorData, `API Request: ${endpoint}`);
        
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      const data = await response.json();
      return data;
    } catch (error) {
      // API Request failed - hata widget'n gster
      errorService.handleAPIError(error, `API Request: ${endpoint}`);
      
      return {
        success: false,
        error: error instanceof Error ? error.message : 'Unknown error'
      };
    }
  }

  async sendMessage(message: string, projectId: number = 1): Promise<ApiResponse> {
    const browserUuid = uuidService.getBrowserUuid();
    
    return this.makeRequest('/chat', {
      method: 'POST',
      headers: {
        'X-Browser-UUID': browserUuid
      },
      body: JSON.stringify({
        message,
        project_id: projectId,
        browser_uuid: browserUuid
      })
    });
  }

  async trackCargo(trackingNumber: string): Promise<ApiResponse> {
    return this.makeRequest('/cargo/track', {
      method: 'POST',
      body: JSON.stringify({
        tracking_number: trackingNumber
      })
    });
  }

  async getWidgetCustomization(): Promise<ApiResponse> {
    return this.makeRequest('/widget-customization', {
      method: 'GET'
    });
  }

  async testEndpoint(endpoint: string): Promise<ApiResponse> {
    return this.makeRequest('/test-endpoint', {
      method: 'POST',
      body: JSON.stringify({
        endpoint
      })
    });
  }

  /**
   * Browser UUID'yi al
   */
  getBrowserUuid(): string {
    return uuidService.getBrowserUuid();
  }

  /**
   * UUID'yi yenile
   */
  refreshBrowserUuid(): string {
    return uuidService.refreshUuid();
  }

  /**
   * UUID debug bilgileri - Production'da kaldrld
   */
  getUuidDebugInfo(): any {
    // Production mode - no debug info
    return null;
  }

  // ===== TOKEN MANAGEMENT METHODS =====

  /**
   * Kullancya token ekle
   */
  async addTokens(userId: number, tokens: number): Promise<ApiResponse<AddTokenResponse>> {
    return this.makeRequest('/add-token', {
      method: 'POST',
      body: JSON.stringify({
        user_id: userId,
        tokens: tokens
      })
    });
  }

  /**
   * Kullancnn token'larn tket
   */
  async exhaustTokens(userId: number): Promise<ApiResponse<ExhaustTokenResponse>> {
    return this.makeRequest('/exhaust-token', {
      method: 'POST',
      body: JSON.stringify({
        user_id: userId
      })
    });
  }

  // ===== SUBSCRIPTION MANAGEMENT METHODS =====

  /**
   * Kullanm bilgilerini getir
   */
  async getUsage(): Promise<ApiResponse<UsageToken>> {
    return this.makeRequest('/subscription/usage', {
      method: 'GET'
    });
  }

  /**
   * Plan listesini getir
   */
  async getPlans(): Promise<ApiResponse<Plan[]>> {
    return this.makeRequest('/subscription/plans', {
      method: 'GET'
    });
  }

  /**
   * Fatura gemiini getir
   */
  async getBillingHistory(): Promise<ApiResponse<any[]>> {
    return this.makeRequest('/subscription/billing-history', {
      method: 'GET'
    });
  }

  // ===== ERROR HANDLING METHODS =====

  /**
   * API hatalarn kontrol et ve uygun mesaj dndr
   */
  handleAPIError(error: any): string {
    if (error?.error) {
      switch (error.error) {
        case 'NO_ACTIVE_PLAN':
          return 'Aktif plannz bulunmuyor. Ltfen bir plan satn aln.';
        case 'USAGE_TOKENS_EXPIRED':
          return 'Kullanm token\'larnzn sresi dolmu. Ltfen plannz yenileyin.';
        case 'INSUFFICIENT_TOKENS':
          return 'Yetersiz kullanm token\'. Ltfen daha fazla token satn aln.';
        case 'TOKEN_VALIDATION_ERROR':
          return 'Token dorulama hatas. Ltfen tekrar deneyin.';
        case 'MISSING_PROJECT_ID':
          return 'Proje ID eksik. Ltfen proje ayarlarnz kontrol edin.';
        case 'PROJECT_NOT_FOUND':
          return 'Proje bulunamad. Ltfen proje ayarlarnz kontrol edin.';
        case 'USER_NOT_FOUND':
          return 'Kullanc bulunamad. Ltfen giri yapn.';
        default:
          return error.message || 'Bilinmeyen bir hata olutu.';
      }
    }
    return error?.message || 'Bilinmeyen bir hata olutu.';
  }

  /**
   * Token durumunu kontrol et
   */
  async checkTokenStatus(): Promise<{ canUse: boolean; error?: string; usage?: UsageToken }> {
    try {
      const usageResponse = await this.getUsage();
      
      if (!usageResponse.success) {
        return {
          canUse: false,
          error: this.handleAPIError(usageResponse)
        };
      }

      const usage = usageResponse.data as UsageToken;
      
      return {
        canUse: usage.can_use_token && !usage.is_expired,
        usage: usage,
        error: usage.can_use_token ? undefined : 'Yetersiz token veya sresi dolmu'
      };
    } catch (error) {
      return {
        canUse: false,
        error: 'Token durumu kontrol edilemedi'
      };
    }
  }
}
