import React, { useState, useEffect, useRef } from 'react';
import { Message, Product, FeedbackData } from '../../../../types';
import './ProductRecommendationMessage.css';
import '../../ProductStyles.css';
import '../../chatbot.css';
import { buildApiUrl, API_CONFIG } from '../../../../config/api'

interface ProductRecommendationMessageProps {
  message: Message;
  onProductClick: (product: Product) => void;
  onFeedback: (feedback: FeedbackData) => void;
  onAddMessage?: (text: string, role: 'user' | 'agent', products?: any[], additionalData?: any) => void;
  onSendMessage?: (text: string) => void;
}

const ProductRecommendationMessage: React.FC<ProductRecommendationMessageProps> = ({ 
  message, 
  onProductClick, 
  onFeedback,
  onAddMessage,
  onSendMessage
}) => {

  // Products' doru ekilde extract et
  const products = message.products || message.data?.products || [];
  
  // Feedback state'i - localStorage'da saklanr
  const [feedbackGiven, setFeedbackGiven] = useState<boolean>(false);
  
  // Scroll container iin ref
  const scrollContainerRef = useRef<HTMLDivElement>(null);
  
  // Component mount olduunda localStorage'dan feedback durumunu kontrol et
  useEffect(() => {
    const feedbackKey = `feedback_${message.id}`;
    const hasFeedback = localStorage.getItem(feedbackKey);
    if (hasFeedback) {
      setFeedbackGiven(true);
    }
  }, [message.id]);

  // Scroll functionality iin useEffect
  useEffect(() => {
    const scrollContainer = scrollContainerRef.current;
    if (!scrollContainer) return;

    // Set CSS variable for product count to calculate proper width
    scrollContainer.style.setProperty('--product-count', products.length.toString());
    
    // Force update the wrapper width calculation
    const wrapper = scrollContainer.querySelector('.products-wrapper') as HTMLElement;
    if (wrapper) {
      const cardWidth = 150;
      const gap = 15;
      const padding = 20;
      const calculatedWidth = (products.length * cardWidth) + ((products.length - 1) * gap) + padding;
      wrapper.style.width = `${calculatedWidth}px`;
      wrapper.style.minWidth = `${calculatedWidth}px`;
      wrapper.style.maxWidth = `${calculatedWidth}px`;
    }

    let isDown = false;
    let startX: number;
    let scrollLeft: number;

    // Mouse events for drag scrolling
    const handleMouseDown = (e: MouseEvent) => {
      isDown = true;
      scrollContainer.style.cursor = 'grabbing';
      startX = e.pageX - scrollContainer.offsetLeft;
      scrollLeft = scrollContainer.scrollLeft;
    };

    const handleMouseLeave = () => {
      isDown = false;
      scrollContainer.style.cursor = 'grab';
    };

    const handleMouseUp = () => {
      isDown = false;
      scrollContainer.style.cursor = 'grab';
    };

    const handleMouseMove = (e: MouseEvent) => {
      if (!isDown) return;
      e.preventDefault();
      const x = e.pageX - scrollContainer.offsetLeft;
      const walk = (x - startX) * 2;
      scrollContainer.scrollLeft = scrollLeft - walk;
    };

    // Touch events for mobile
    const handleTouchStart = (e: TouchEvent) => {
      startX = e.touches[0].pageX - scrollContainer.offsetLeft;
      scrollLeft = scrollContainer.scrollLeft;
    };

    const handleTouchMove = (e: TouchEvent) => {
      if (!startX) return;
      e.preventDefault();
      const x = e.touches[0].pageX - scrollContainer.offsetLeft;
      const walk = (x - startX) * 2;
      scrollContainer.scrollLeft = scrollLeft - walk;
    };

    const handleTouchEnd = () => {
      startX = 0;
    };

    // Event listeners ekle
    scrollContainer.addEventListener('mousedown', handleMouseDown);
    scrollContainer.addEventListener('mouseleave', handleMouseLeave);
    scrollContainer.addEventListener('mouseup', handleMouseUp);
    scrollContainer.addEventListener('mousemove', handleMouseMove);
    scrollContainer.addEventListener('touchstart', handleTouchStart);
    scrollContainer.addEventListener('touchmove', handleTouchMove);
    scrollContainer.addEventListener('touchend', handleTouchEnd);

    // Cleanup function
    return () => {
      scrollContainer.removeEventListener('mousedown', handleMouseDown);
      scrollContainer.removeEventListener('mouseleave', handleMouseLeave);
      scrollContainer.removeEventListener('mouseup', handleMouseUp);
      scrollContainer.removeEventListener('mousemove', handleMouseMove);
      scrollContainer.removeEventListener('touchstart', handleTouchStart);
      scrollContainer.removeEventListener('touchmove', handleTouchMove);
      scrollContainer.removeEventListener('touchend', handleTouchEnd);
    };
  }, [products.length]);

  // Scroll container geniliini kontrol et ve scroll'u etkinletir
  useEffect(() => {
    const scrollContainer = scrollContainerRef.current;
    if (!scrollContainer) return;

    // Scroll container'n scroll zelliklerini kontrol et
    const checkScrollability = () => {
      const wrapper = scrollContainer.querySelector('.products-wrapper') as HTMLElement;
      if (wrapper) {
        const containerWidth = scrollContainer.clientWidth;
        const contentWidth = wrapper.scrollWidth;
        
        // Eer ierik container'dan genise scroll etkinletir
        if (contentWidth > containerWidth) {
          scrollContainer.style.overflowX = 'auto';
        } else {
          scrollContainer.style.overflowX = 'hidden';
        }
      }
    };

    // lk kontrol
    checkScrollability();

    // Resize observer ile dinamik kontrol
    const resizeObserver = new ResizeObserver(checkScrollability);
    resizeObserver.observe(scrollContainer);

    return () => {
      resizeObserver.disconnect();
    };
  }, [products.length]);
  
  /**
   * Product action handler - view, compare actions
   */
  const handleProductAction = async (product: Product, action: 'view' | 'compare') => {
    try {
      const sessionId = localStorage.getItem('chat_session_id') || 'unknown';
      
      // Product interaction' API'ye gnder
      const response = await fetch(buildApiUrl('/api/product-interaction'), {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          session_id: sessionId,
          product_id: product.id,
          action: action,
          timestamp: new Date().toISOString(),
          source: 'chat_widget',
          metadata: {
            product_name: product.name,
            product_category: product.category,
            product_brand: product.brand,
            action_source: 'chat_widget'
          }
        })
      });

      if (response.ok) {
        await response.json();

        
        // Action'a gre ilem yap
        if (action === 'view') {
          // rn detay widget'n gster
          if (onAddMessage) {
            // ProductDetailMessage widget'n ekle
            onAddMessage(
              ` **${product.name}** rn detaylar ykleniyor...`,
              'agent',
              [product],
              { 
                type: 'product_detail',
                product: product,
                action: 'view',
                widget: 'ProductDetailMessage'
              }
            );
          }
        } else if (action === 'compare') {
          // Karlatrma ilemi
          if (onAddMessage) {
            const compareMessage = ` **${product.name}** rnn karlatrmak istiyorsunuz.\n\nBu rn baka rnlerle karlatrmak iin hangi zellikleri nemsiyorsunuz?`;
            onAddMessage(compareMessage, 'agent', [product]);
          }
        }
        
      } else {
        // Product interaction logging failed
      }
      
    } catch (error) {
      // Product action error
    }
  };

  const handleFeedback = (isHelpful: boolean) => {
    // Feedback'i localStorage'a kaydet
    const feedbackKey = `feedback_${message.id}`;
    localStorage.setItem(feedbackKey, JSON.stringify({
      isHelpful,
      timestamp: new Date().toISOString()
    }));
    
    // State'i gncelle
    setFeedbackGiven(true);
    
    // Parent component'e feedback'i gnder
    onFeedback({
      messageId: message.id,
      isHelpful,
      comment: ''
    });
  };

  return (
    <div className="message agent-message">
      {/* Message Content Wrapper */}
      <div className="message-content-wrapper">
        {/* rn nerisi bal */}
        <h4 style={{ fontSize: '14px', fontWeight: '600', marginBottom: '8px' }}>
          {message.data?.title || 'Senin iin nerdiim rnler:'}
        </h4>
        
        {/* rn kartlar */}
        {products && products.length > 0 ? (
          <div className="products-scroll-container" ref={scrollContainerRef}>
            <div className="products-wrapper">
              {products.map((product: Product, index: number) => (
                <div key={`${product.id}-${index}`} className="product-card">
                  <div className="product-image">
                    <img 
                      src={product.image || API_CONFIG.PRODUCT_IMAGE_PATH + 'default-product.svg'} 
                      alt={product.name}
                      onError={(e) => {
                        // Try different fallback images
                        const currentSrc = e.currentTarget.src;
                        if (currentSrc.includes('default-product.svg')) {
                          // If SVG fails, try JPG
                          e.currentTarget.src = API_CONFIG.PRODUCT_IMAGE_PATH + 'default-product.jpg';
                        } else if (currentSrc.includes('default-product.jpg')) {
                          // If JPG also fails, use a data URL placeholder
                          e.currentTarget.src = 'data:image/svg+xml;base64,';
                        }
                        e.currentTarget.onerror = null; // Sonsuz dngy nle
                      }}
                    />
                  </div>
                  <div className="product-info">
                    <h4>{product.name}</h4>
                    <p style={{ fontSize: '12px', color: '#6B7280', marginBottom: '4px' }}>
                      {product.brand}
                    </p>
                    <p className="product-price">{product.price}</p>
                    <div style={{ display: 'flex', alignItems: 'center', gap: '4px', marginBottom: '8px' }}>
                      <span style={{ fontSize: '10px', color: '#6B7280' }}>Rating:</span>
                      <span style={{ fontSize: '10px', color: '#F59E0B' }}> {product.rating}</span>
                    </div>
                    
                    {/* Enhanced Product Buttons */}
                    <div className="product-buttons-container" style={{ display: 'flex', gap: '6px' }}>
                      <button 
                        className="product-button primary"
                        onClick={(e) => {
                          e.stopPropagation();
                          handleProductAction(product, 'view');
                        }}
                        style={{
                          flex: 1,
                          padding: '6px 8px',
                          fontSize: '11px',
                          backgroundColor: '#3B82F6',
                          color: 'white',
                          border: 'none',
                          borderRadius: '4px',
                          cursor: 'pointer'
                        }}
                      >
                        Detaylar
                      </button>
                      <button 
                        className="product-button secondary"
                        onClick={(e) => {
                          e.stopPropagation();
                          handleProductAction(product, 'compare');
                        }}
                        style={{
                          flex: 1,
                          padding: '6px 8px',
                          fontSize: '11px',
                          backgroundColor: '#F59E0B',
                          color: 'white',
                          border: 'none',
                          borderRadius: '4px',
                          cursor: 'pointer'
                        }}
                      >
                        Karlatr
                      </button>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        ) : (
          <div className="no-products-message">
            <div className="no-products-icon"></div>
            <p>{message.content || 'rn bulunamad.'}</p>
            {message.data?.suggestions && message.data.suggestions.length > 0 && (
              <div className="suggestions">
                <p className="suggestions-title">Size unlar nerebilirim:</p>
                <div className="suggestion-buttons">
                  {message.data.suggestions.map((suggestion: string, index: number) => (
                    <button
                      key={index}
                      className="suggestion-button"
                      onClick={() => {
                        // Suggestion'a "ner" kelimesini ekle ve AI'ya gnder
                        const messageWithRecommendation = `${suggestion} ner`;
                        if (onSendMessage) {
                          onSendMessage(messageWithRecommendation);
                        } else if (onAddMessage) {
                          onAddMessage(messageWithRecommendation, 'user');
                        }
                      }}
                    >
                      {suggestion}
                    </button>
                  ))}
                </div>
              </div>
            )}
          </div>
        )}
        
        {/* AI notu - sadece rn varsa gster */}
        {products.length > 0 && (
          <div className="ai-note">
            <span className="ai-icon">AI</span>
            {message.data?.ai_note || 'Tercihlerinize gre nerilen rnler.'}
          </div>
        )}
      </div>
      
      {/* Feedback Buttons - Sadece feedback verilmemise gster */}
      {!feedbackGiven && (
        <div className="feedback-container">
          <button 
            className="feedback-button"
            onClick={() => handleFeedback(true)}
          >
             Yararl
          </button>
          <button 
            className="feedback-button negative"
            onClick={() => handleFeedback(false)}
          >
             Yararsz
          </button>
        </div>
      )}
      
      {/* Feedback verildiyse teekkr mesaj gster */}
      {feedbackGiven && (
        <div className="feedback-thanks">
          <span style={{ fontSize: '12px', color: '#10B981', fontStyle: 'italic' }}>
             Feedback'iniz iin teekkrler!
          </span>
        </div>
      )}
    </div>
  );
};

export default ProductRecommendationMessage;
