import React from 'react';
import { MessageListProps, WidgetCustomization } from '../../types';
import { getAILogoUrls } from '../../utils/imageUtils';
import MessageItem from './MessageItem';
import WelcomeMessage from './WelcomeMessage';
import { useChat } from '../../hooks/useChat';
import './chatbot.css';
// import ShimmerMessage from './ShimmerMessage';

interface ExtendedMessageListProps extends MessageListProps {
  widgetCustomization?: WidgetCustomization;
  onAddMessage?: (text: string, role: 'user' | 'agent', products?: any[], additionalData?: any) => void;
  onSendMessage?: (text: string) => void;
}

const MessageList: React.FC<ExtendedMessageListProps> = ({ messages, onProductClick, onFeedback, welcomeMessage: customWelcomeMessage, widgetCustomization, onAddMessage, onSendMessage }) => {
  const { chatContentRef } = useChat();

  // Default welcome message
  const agentName = widgetCustomization?.ai_name || 'ConvState AI';
  const defaultWelcomeMessage = `Merhaba ben ${agentName}, sizin tercihlerinize gre rn nerileri sunuyorum. Size nasl yardmc olabilirim?`;
  const welcomeText = customWelcomeMessage || defaultWelcomeMessage;



  // Typing indicator - artk messages array'inde shimmer message'lar var
  // const typingIndicator = isTyping ? (
  //   <div className="message-group">
  //     <div className="agent-avatar">
  //       <img src="/imgs/ai-conversion-logo-small.svg" alt="AI Conversion Agent" />
  //     </div>
  //     <ShimmerMessage type="general" />
  //   </div>
  // ) : null;

  return (
    <div className="chat-content" ref={chatContentRef}>
      {/* Welcome Message */}
      <WelcomeMessage 
        welcomeText={welcomeText}
        agentName={agentName}
        logoUrl={getAILogoUrls().small}
      />
      
      {/* Messages from props */}
      {messages.map((message) => (
        <MessageItem
          key={message.id}
          message={message}
          onProductClick={onProductClick}
          onFeedback={onFeedback}
          onAddMessage={onAddMessage}
          onSendMessage={onSendMessage}
        />
      ))}
      
      {/* Typing Indicator - artk messages array'inde shimmer message'lar var */}
      {/* {typingIndicator} */}
    </div>
  );
};

export default MessageList;
