import React, { useState, useEffect } from 'react';
import { Message, Product } from '../../../../types';
import './ProductDetailMessage.css';
import '../../ProductStyles.css';
import '../../chatbot.css';
import { buildApiUrl } from '../../../../config/api';

interface ProductDetailMessageProps {
  message: Message;
  onProductClick: (product: Product) => void;
  onAddMessage?: (text: string, role: 'user' | 'agent', products?: any[], additionalData?: any) => void;
}

const ProductDetailMessage: React.FC<ProductDetailMessageProps> = ({ 
  message, 
  onProductClick,
  onAddMessage
}) => {
  const [isLoading, setIsLoading] = useState(false);
  const [productDetails, setProductDetails] = useState<any>(null);
  const [error, setError] = useState<string | null>(null);

  // Extract product from message
  const product = message.data?.product || message.products?.[0];
  
  useEffect(() => {
    if (product && !productDetails) {
      fetchProductDetails();
    }
  }, [product]);

  const fetchProductDetails = async () => {
    if (!product) return;
    
    setIsLoading(true);
    setError(null);
    
    try {
      const sessionId = localStorage.getItem('chat_session_id') || 'unknown';
      
      const response = await fetch(buildApiUrl('/api/product-details'), {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          session_id: sessionId,
          product_id: product.id,
          product_name: product.name,
          product_description: product.description,
          product_price: product.price,
          product_category: product.category,
          action: 'get_details'
        })
      });

      if (!response.ok) {
        // Handle different HTTP status codes
        if (response.status === 404) {
          throw new Error('rn detaylar bulunamad. Ltfen daha sonra tekrar deneyin.');
        } else if (response.status === 429) {
          throw new Error('Gnlk grntleme limitiniz doldu. Yarn tekrar deneyin.');
        } else if (response.status === 500) {
          throw new Error('Sunucu hatas olutu. Ltfen daha sonra tekrar deneyin.');
        } else {
          throw new Error(`Sunucu hatas (${response.status}). Ltfen daha sonra tekrar deneyin.`);
        }
      }

      const data = await response.json();
      
      if (data.success) {
        setProductDetails(data.data);
      } else {
        // Handle different error types from the API
        if (data.message === 'Session not found') {
          throw new Error('Oturum bulunamad. Ltfen sayfay yenileyin.');
        } else if (data.message === 'Daily view limit reached') {
          throw new Error('Gnlk grntleme limitiniz doldu. Yarn tekrar deneyin.');
        } else {
          throw new Error(data.message || 'rn detaylar yklenirken bir hata olutu.');
        }
      }
    } catch (err) {
      console.error('Error fetching product details:', err);
      
      // Provide more user-friendly error messages
      let errorMessage = 'rn detaylar yklenirken bir hata olutu.';
      
      if (err instanceof Error) {
        if (err.message.includes('Failed to fetch') || err.message.includes('NetworkError')) {
          errorMessage = 'nternet balantnz kontrol edin ve tekrar deneyin.';
        } else if (err.message.includes('timeout')) {
          errorMessage = 'stek zaman amna urad. Ltfen tekrar deneyin.';
        } else {
          errorMessage = err.message;
        }
      }
      
      setError(errorMessage);
    } finally {
      setIsLoading(false);
    }
  };

  const handleAddToCart = () => {
    if (onAddMessage) {
      onAddMessage(
        `Sepete ekle: ${product?.name}`,
        'user',
        undefined,
        { action: 'add_to_cart', product: product }
      );
    }
  };

  const handleBuyNow = () => {
    if (onAddMessage) {
      onAddMessage(
        `Satn al: ${product?.name}`,
        'user',
        undefined,
        { action: 'buy_now', product: product }
      );
    }
  };

  const handleCompare = () => {
    if (onAddMessage) {
      onAddMessage(
        `Karlatr: ${product?.name}`,
        'user',
        undefined,
        { action: 'compare', product: product }
      );
    }
  };

  const handlePriceComparison = async () => {
    if (!product || !onAddMessage) return;
    
    try {
      const sessionId = localStorage.getItem('chat_session_id') || 'unknown';
      
      const response = await fetch(buildApiUrl('/api/price-comparison'), {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          session_id: sessionId,
          product_id: product.id,
          product_name: product.name,
          product_price: product.price,
          product_category: product.category,
          comparison_type: 'similar_products'
        })
      });

      if (response.ok) {
        const data = await response.json();
        if (data.success) {
          // Fiyat karlatrma sonularn gster
          onAddMessage(
            ` Fiyat Karlatrmas: ${product.name}`,
            'agent',
            undefined,
            { 
              action: 'price_comparison_result', 
              product: product,
              comparison_data: data.data
            }
          );
        } else {
          onAddMessage(
            ` Fiyat karlatrmas yaplamad: ${data.message}`,
            'agent'
          );
        }
      } else {
        onAddMessage(
          ` Fiyat karlatrmas hatas: ${response.status}`,
          'agent'
        );
      }
    } catch (error) {
      console.error('Price comparison error:', error);
      onAddMessage(
        ` Fiyat karlatrmas yaplrken bir hata olutu.`,
        'agent'
      );
    }
  };

  if (isLoading) {
    return (
      <div className="product-detail-message">
        <div className="product-detail-loading">
          <div className="loading-spinner"></div>
          <p>rn detaylar ykleniyor...</p>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="product-detail-message">
        <div className="product-detail-error">
          <div className="error-icon">
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
              <circle cx="12" cy="12" r="10" stroke="#ef4444" strokeWidth="2"/>
              <line x1="15" y1="9" x2="9" y2="15" stroke="#ef4444" strokeWidth="2"/>
              <line x1="9" y1="9" x2="15" y2="15" stroke="#ef4444" strokeWidth="2"/>
            </svg>
          </div>
          <div className="error-content">
            <h4>rn Detaylar Yklenemedi</h4>
            <p>{error}</p>
            <div className="error-actions">
              <button onClick={fetchProductDetails} className="retry-button">
                <svg width="16" height="16" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                  <path d="M1 4v6h6" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
                  <path d="M23 20v-6h-6" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
                  <path d="M20.49 9A9 9 0 0 0 5.64 5.64L1 10m22 4l-4.64 4.36A9 9 0 0 1 3.51 15" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
                </svg>
                Tekrar Dene
              </button>
              <button onClick={() => setError(null)} className="dismiss-button">
                <svg width="16" height="16" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                  <line x1="18" y1="6" x2="6" y2="18" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
                  <line x1="6" y1="6" x2="18" y2="18" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
                </svg>
                Kapat
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  }

  if (!product) {
    return (
      <div className="product-detail-message">
        <div className="product-detail-error warning">
          <div className="error-icon">
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
              <circle cx="12" cy="12" r="10" stroke="#f59e0b" strokeWidth="2"/>
              <line x1="12" y1="8" x2="12" y2="12" stroke="#f59e0b" strokeWidth="2" strokeLinecap="round"/>
              <line x1="12" y1="16" x2="12.01" y2="16" stroke="#f59e0b" strokeWidth="2" strokeLinecap="round"/>
            </svg>
          </div>
          <div className="error-content">
            <h4>rn Bilgisi Bulunamad</h4>
            <p>Bu rn iin detay bilgisi mevcut deil. Ltfen farkl bir rn sein.</p>
          </div>
        </div>
      </div>
    );
  }

  const details = productDetails || {};

  return (
    <div className="product-detail-message">
      <div className="product-detail-container">
        {/* Product Header */}
        <div className="product-detail-header">
          <div className="product-detail-image">
            <img 
              src={product.image || '/imgs/default-product.svg'} 
              alt={product.name}
              onError={(e) => {
                e.currentTarget.src = '/imgs/default-product.svg';
              }}
            />
          </div>
          <div className="product-detail-info">
            <h3 className="product-detail-title">{product.name}</h3>
            <div className="product-detail-price">
              {product.price && (
                <span className="price">
                  {typeof product.price === 'string' && product.price.includes('$') ? (
                    <>
                      <span className="currency">$</span>
                      <span className="amount">{product.price.replace('$', '')}</span>
                    </>
                  ) : (
                    <>
                      <span className="amount">{product.price}</span>
                      <span className="currency"></span>
                    </>
                  )}
                </span>
              )}
            </div>
          </div>
        </div>

        {/* AI Generated Content */}
        {details.ai_description && (
          <div className="product-detail-ai-content">
            <div className="ai-header">
              <img src="http://127.0.0.1:8000/imgs/ai-conversion-logo.svg" alt="ConvState AI" className="ai-logo" />
              <h4>ConvState AI Analizi</h4>
            </div>
            <div className="ai-description">
              {details.ai_description}
            </div>
          </div>
        )}

        {/* Product Features */}
        {details.features && details.features.length > 0 && (
          <div className="product-detail-features">
            <h4> zellikler</h4>
            <ul className="features-list">
              {details.features.map((feature: string, index: number) => (
                <li key={index}>{feature}</li>
              ))}
            </ul>
          </div>
        )}

        {/* Usage Scenarios */}
        {details.usage_scenarios && details.usage_scenarios.length > 0 && (
          <div className="product-detail-usage">
            <h4> Kullanm Alanlar</h4>
            <ul className="usage-list">
              {details.usage_scenarios.map((scenario: string, index: number) => (
                <li key={index}>{scenario}</li>
              ))}
            </ul>
          </div>
        )}

        {/* Technical Specifications */}
        {details.specifications && Object.keys(details.specifications).length > 0 && (
          <div className="product-detail-specs">
            <h4> Teknik zellikler</h4>
            <div className="specs-grid">
              {Object.entries(details.specifications).map(([key, value]) => (
                <div key={key} className="spec-item">
                  <span className="spec-key">{key}:</span>
                  <span className="spec-value">{value as string}</span>
                </div>
              ))}
            </div>
          </div>
        )}

        {/* Recommendations */}
        {details.recommendations && details.recommendations.length > 0 && (
          <div className="product-detail-recommendations">
            <h4> neriler</h4>
            <ul className="recommendations-list">
              {details.recommendations.map((rec: string, index: number) => (
                <li key={index}>{rec}</li>
              ))}
            </ul>
          </div>
        )}

        {/* Action Buttons */}
        <div className="product-detail-actions">
          <div className="action-buttons-row">
            <button 
              className="action-button primary"
              onClick={handleAddToCart}
            >
               Sepete Ekle
            </button>
            <button 
              className="action-button secondary"
              onClick={handleBuyNow}
            >
               Hemen Satn Al
            </button>
          </div>
          <button 
            className="action-button tertiary full-width"
            onClick={handlePriceComparison}
          >
             Fiyat Karlatr
          </button>
        </div>

      </div>
    </div>
  );
};

export default ProductDetailMessage;