import React from 'react';
import { Message, FeedbackData } from '../../../../types';
import './FeedbackMessage.css';
import '../../chatbot.css';

interface FeedbackMessageProps {
  message: Message;
  onFeedback: (feedback: FeedbackData) => void;
}

const FeedbackMessage: React.FC<FeedbackMessageProps> = ({ message, onFeedback }) => {
  
  const handleFeedback = (isHelpful: boolean) => {
    onFeedback({
      messageId: message.id,
      isHelpful,
      comment: ''
    });
  };

  return (
    <div className="message agent-message">
      {/* Message Content Wrapper */}
      <div className="message-content-wrapper">
        {/* Feedback mesaj */}
        <p>{message.content || message.message || 'Feedback mesaj bulunamad.'}</p>
      </div>
      
      {/* Feedback butonlar */}
      <div className="feedback-container">
        <button 
          className="feedback-button"
          onClick={() => handleFeedback(true)}
        >
           Evet, yardmc oldu
        </button>
        <button 
          className="feedback-button negative"
          onClick={() => handleFeedback(false)}
        >
           Hayr, yardmc olmad
        </button>
      </div>
    </div>
  );
};

export default FeedbackMessage;
