import React from 'react';
import { API_CONFIG } from '../config/api';

/**
 * Resim ykleme hatalarn handle eden utility fonksiyonlar
 */

/**
 * Resim ykleme hatas durumunda fallback resmi dndrr
 * @param event - onError event'i
 * @param fallbackSrc - Fallback resim yolu (varsaylan: API_CONFIG.PRODUCT_IMAGE_PATH + 'default-product.svg')
 */
export const handleImageError = (
  event: React.SyntheticEvent<HTMLImageElement, Event>,
  fallbackSrc: string = API_CONFIG.PRODUCT_IMAGE_PATH + 'default-product.svg'
): void => {
  const target = event.currentTarget;
  target.src = fallbackSrc;
  target.onerror = null; // Sonsuz dngy nle
};

/**
 * rn resmi iin gvenli src oluturur
 * @param imageSrc - Orijinal resim yolu
 * @param fallbackSrc - Fallback resim yolu (varsaylan: API_CONFIG.PRODUCT_IMAGE_PATH + 'default-product.svg')
 * @returns Gvenli resim yolu
 */
export const getSafeImageSrc = (
  imageSrc: string | undefined | null,
  fallbackSrc: string = API_CONFIG.PRODUCT_IMAGE_PATH + 'default-product.svg'
): string => {
  if (!imageSrc || imageSrc.trim() === '') {
    return fallbackSrc;
  }
  return imageSrc;
};

/**
 * Resim ykleme durumunu takip eden hook
 * @param initialSrc - Balang resim yolu
 * @returns Resim yolu ve hata durumu
 */
export const useImageLoader = (initialSrc: string) => {
  const [src, setSrc] = React.useState(initialSrc);
  const [hasError, setHasError] = React.useState(false);

  const handleError = React.useCallback(() => {
    setSrc(API_CONFIG.PRODUCT_IMAGE_PATH + 'default-product.svg');
    setHasError(true);
  }, []);

  const reset = React.useCallback(() => {
    setSrc(initialSrc);
    setHasError(false);
  }, [initialSrc]);

  return { src, hasError, handleError, reset };
};

/**
 * Logo URL'lerini oluturur
 * @param logoName - Logo dosya ad (rn: 'ai-conversion-logo.svg')
 * @returns Tam logo URL'i
 */
export const getLogoUrl = (logoName: string): string => {
  // Eer window.convstateaiConfig varsa baseUrl'den al
  if (typeof window !== 'undefined' && (window as any).convstateaiConfig) {
    const config = (window as any).convstateaiConfig;
    const baseUrl = config.baseUrl || getCurrentDomain();
    return `${baseUrl}/widgetcust/imgs/${logoName}`;
  }
  
  // Fallback olarak relative path kullan
  return `/imgs/${logoName}`;
};

/**
 * Mevcut domain'i otomatik olarak belirle
 */
const getCurrentDomain = (): string => {
  // Gvenli production domain - her zaman convstateai.com kullan
  return 'https://convstateai.com';
};

/**
 * AI Conversion logo URL'lerini dndrr
 */
export const getAILogoUrls = () => {
  return {
    small: getLogoUrl('ai-conversion-logo-small.svg'),
    large: getLogoUrl('ai-conversion-logo.svg')
  };
};

