import React from 'react';
import { Message } from '../../../types';
import './ErrorMessage.css';
import '../chatbot.css';

interface ErrorMessageProps {
  message: Message;
}

const ErrorMessage: React.FC<ErrorMessageProps> = ({ message }) => {
  const errorData = message.data || {};
  const errorType = errorData.error || 'UNKNOWN_ERROR';
  
  // API'den dnen hata mesajn ncelikle kullan
  let errorMessage = 'Bir hata olutu';
  
  // Eer message.data iinde success: false varsa, message.message' kullan
  if (errorData.success === false && message.message) {
    errorMessage = message.message;
  } else if (errorData.message) {
    // Eer errorData iinde message varsa onu kullan
    errorMessage = errorData.message;
  } else if (message.message) {
    // Son olarak message.message' kullan
    errorMessage = message.message;
  }
  
  const redirectUrl = errorData.redirect;

  const getErrorIcon = () => {
    switch (errorType) {
      case 'NO_USAGE_TOKENS':
        return '';
      case 'USAGE_TOKENS_EXPIRED':
        return '';
      case 'INSUFFICIENT_TOKENS':
        return '';
      case 'INVALID_PROJECT':
        return '';
      default:
        return '';
    }
  };

  const getErrorTitle = () => {
    switch (errorType) {
      case 'NO_USAGE_TOKENS':
        return 'Token Bulunamad';
      case 'USAGE_TOKENS_EXPIRED':
        return 'Token Sresi Dolmu';
      case 'INSUFFICIENT_TOKENS':
        return 'Yetersiz Token';
      case 'INVALID_PROJECT':
        return 'Geersiz Proje';
      default:
        return 'Hata';
    }
  };

  const getErrorDescription = () => {
    // zel hata trleri iin nceden tanmlanm mesajlar
    switch (errorType) {
      case 'NO_USAGE_TOKENS':
        return 'Kullanm token\'nz bulunmuyor. rn kullanmak iin satn almanz gerekiyor.';
      case 'USAGE_TOKENS_EXPIRED':
        return 'Abonelik sreniz dolmu. rn kullanmak iin satn almanz gerekiyor.';
      case 'INSUFFICIENT_TOKENS':
        return 'Yetersiz kullanm token\'. Ltfen rn kullanm iin token satn almanz gerekiyor.';
      case 'INVALID_PROJECT':
        return errorMessage; // API'den dnen mesaj gster
      default:
        return errorMessage; // API'den dnen mesaj gster
    }
  };

  const getTokenInfo = () => {
    if (errorType === 'INSUFFICIENT_TOKENS' && errorData.tokens_remaining !== undefined) {
      return (
        <div className="token-info">
          <div className="token-stats">
            <div className="token-stat">
              <span className="label">Kalan Token:</span>
              <span className="value">{errorData.tokens_remaining}</span>
            </div>
            <div className="token-stat">
              <span className="label">Kullanlan:</span>
              <span className="value">{errorData.tokens_used}</span>
            </div>
            <div className="token-stat">
              <span className="label">Toplam:</span>
              <span className="value">{errorData.tokens_total}</span>
            </div>
          </div>
          <div className="usage-bar">
            <div 
              className="usage-fill" 
              style={{ width: `${errorData.usage_percentage || 0}%` }}
            ></div>
          </div>
        </div>
      );
    }
    return null;
  };

  const handleUpgrade = () => {
    if (redirectUrl) {
      window.open(redirectUrl, '_blank');
    }
  };

  return (
    <div className="error-message">
      <div className="error-header">
        <div className="error-icon">{getErrorIcon()}</div>
        <div className="error-content">
          <h3 className="error-title">{getErrorTitle()}</h3>
          <p className="error-description">{getErrorDescription()}</p>
        </div>
      </div>
      
      {getTokenInfo()}
      
      {redirectUrl && (
        <div className="error-actions">
          <button 
            className="upgrade-button"
            onClick={handleUpgrade}
          >
            Plan Gncelle
          </button>
        </div>
      )}
      
    </div>
  );
};

export default ErrorMessage;
